<template>
  <div class="regional-page">
    <div class="container mx-auto px-4 py-6">
      <h1 class="text-2xl font-bold text-white mb-6">{{ stateName }}</h1>
      
      <!-- Loading State -->
      <div v-if="loading" class="flex justify-center items-center py-8">
        <div class="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-purple-500"></div>
      </div>

      <!-- Error State -->
      <div v-if="error" class="text-red-500 text-center py-8">
        {{ error }}
      </div>

      <!-- Content -->
      <div v-if="!loading && !error" class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        <!-- Add your regional content here -->
        <pre class="text-white">{{ stateDetails }}</pre>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'RegionalPage',
  data() {
    return {
      stateId: null,
      stateName: '',
      stateDetails: null,
      loading: true,
      error: null
    }
  },
  async created() {
    // Get the state ID from route params
    this.stateId = this.$route.params.stateId
    
    try {
      // Fetch state details using the ID
      const response = await axios.get(`https://ott.no1news.in/api/frontend/state/${this.stateId}`)
      console.log('API Response:', response.data) // Debug log
      
      if (response.data) {
        this.stateDetails = response.data
        this.stateName = response.data.name || 'State Details'
      } else {
        this.error = 'No data received from server'
      }
    } catch (err) {
      console.error('Error fetching state details:', err.response || err)
      this.error = err.response?.data?.message || 'Error loading state details. Please try again later.'
    } finally {
      this.loading = false
    }
  }
}
</script>

<style scoped>
.regional-page {
  min-height: 100vh;
}
</style> 