<script>
import { useStore } from 'vuex';
import { ref, computed, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';

export default {
  name: 'SubscriptionPlans',
  setup() {
    const store = useStore();
    const router = useRouter();
    const showPaymentModal = ref(false);
    const showCodeInput = ref(false);
    const subscriptionCode = ref('');
    const codeError = ref('');
    const isValidCode = ref(false);

    // Define default plan
    const defaultPlan = {
      id: 'premium_365',
      name: 'Premium 365',
      price: 365,
      duration: 365
    };

    const selectedPlan = ref(defaultPlan);

    // Use computed properties from store
    const isAuthenticated = computed(() => store.getters.isAuthenticated);
    const user = computed(() => store.getters.currentUser);
    const token = computed(() => store.getters.token);

    // Initialize store on component mount
    onMounted(() => {
      store.dispatch('initializeStore');
      
      // Check for plan in URL
      const urlParams = new URLSearchParams(window.location.search);
      const planType = urlParams.get('plan');
      if (planType) {
        const plan = { ...defaultPlan };
        plan.id = planType;
        selectedPlan.value = plan;
      }
    });

    const handlePaymentGateway = async () => {
      try {
        console.log('Payment gateway initiated');
        console.log('Auth status:', isAuthenticated.value);
        console.log('User:', user.value);
        console.log('Token:', token.value);

        // Check authentication and token
        if (!isAuthenticated.value || !user.value || !token.value) {
          console.log('User not authenticated or missing token, redirecting to login');
          router.push({
            path: '/login',
            query: { 
              redirect: '/plans',
              plan: selectedPlan.value.id
            }
          });
          return;
        }

        // Set authorization header
        axios.defaults.headers.common['Authorization'] = `Bearer ${token.value}`;

        // Show loading state
        store.commit('setLoading', true);
        
        console.log('Creating order for plan:', selectedPlan.value);
        
        try {
          // Create Razorpay order
          const orderResponse = await axios.post('/api/payment/create-order/', {
            amount: selectedPlan.value.price,
            currency: 'INR',
            plan_id: selectedPlan.value.id
          });

          console.log('Order created:', orderResponse.data);

          // Hide loading state
          store.commit('setLoading', false);

          if (!orderResponse.data || !orderResponse.data.key_id) {
            throw new Error('Invalid order response from server');
          }

          // Initialize Razorpay
          const options = {
            key: orderResponse.data.key_id,
            amount: orderResponse.data.amount,
            currency: orderResponse.data.currency,
            name: 'No1 OTT',
            description: `${selectedPlan.value.name} Subscription`,
            order_id: orderResponse.data.order_id,
            prefill: {
              name: user.value?.first_name || '',
              email: user.value?.email || '',
              contact: user.value?.phone || ''
            },
            theme: {
              color: '#c30059'
            },
            handler: async (response) => {
              try {
                store.commit('setLoading', true);
                console.log('Payment successful, verifying:', response);
                
                const verifyResponse = await axios.post('/api/payment/verify-payment/', {
                  razorpay_payment_id: response.razorpay_payment_id,
                  razorpay_order_id: response.razorpay_order_id,
                  razorpay_signature: response.razorpay_signature,
                  plan_id: selectedPlan.value.id,
                  phone: user.value.phone
                });

                if (verifyResponse.data.status === 'success') {
                  // Update user subscription in store
                  store.commit('setSubscription', verifyResponse.data.subscription);
                  alert('Payment successful! Your subscription is now active.');
                  router.push('/profile');
                } else {
                  throw new Error('Payment verification failed');
                }
              } catch (error) {
                console.error('Payment verification failed:', error);
                alert('Payment verification failed. Please contact support.');
              } finally {
                store.commit('setLoading', false);
              }
            }
          };

          // Create and open Razorpay
          console.log('Opening Razorpay with options:', options);
          const rzp = new window.Razorpay(options);
          rzp.on('payment.failed', function (response) {
            console.error('Payment failed:', response.error);
            store.commit('setLoading', false);
            alert(`Payment failed: ${response.error.description}`);
          });
          
          rzp.open();
        } catch (error) {
          console.error('Order creation failed:', error);
          store.commit('setLoading', false);
          
          if (error.response) {
            if (error.response.status === 401) {
              // Clear invalid session and redirect to login
              store.dispatch('logout');
              router.push({
                path: '/login',
                query: { 
                  redirect: '/plans',
                  plan: selectedPlan.value.id
                }
              });
            } else {
              alert(`Server error: ${error.response.data.message || 'Please try again later'}`);
            }
          } else if (error.request) {
            alert('Network error. Please check your internet connection.');
          } else {
            alert('Unable to create order. Please try again.');
          }
        }
      } catch (error) {
        console.error('Payment gateway error:', error);
        store.commit('setLoading', false);
        alert('Something went wrong. Please try again.');
      }
    };

    const validateCode = () => {
      subscriptionCode.value = subscriptionCode.value.toUpperCase();
      
      // Basic validation - you can add more complex validation rules
      if (subscriptionCode.value.length < 8) {
        codeError.value = 'Code must be at least 8 characters';
        isValidCode.value = false;
      } else if (!/^[A-Z0-9-]+$/.test(subscriptionCode.value)) {
        codeError.value = 'Code can only contain letters, numbers and hyphens';
        isValidCode.value = false;
      } else {
        codeError.value = '';
        isValidCode.value = true;
      }
    };

    const handleCodeSubmit = async () => {
      if (!isValidCode.value) return;

      try {
        // Here you would make an API call to validate and activate the code
        // const response = await this.validateSubscriptionCode(this.subscriptionCode);
        
        // For now, we'll just show a success message
        showCodeInput.value = false;
        // You might want to show a success message or redirect to the content page
        router.push('/dashboard');
      } catch (error) {
        codeError.value = 'Invalid or expired code. Please try again.';
      }
    };

    return {
      selectedPlan,
      showPaymentModal,
      showCodeInput,
      subscriptionCode,
      codeError,
      isValidCode,
      handlePaymentGateway,
      validateCode,
      handleCodeSubmit
    };
  }
}
</script>

