import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import LoginPage from '../components/LoginPage.vue'
import SubscriptionPlans from '../views/SubscriptionPlans.vue'
import VideoPlayer from '../views/VideoPlayer.vue'
import MovieDetails from '../views/MovieDetails.vue'
import RegionalContent from '../views/RegionalContent.vue'
import SearchPage from '@/views/SearchPage.vue'
import RegionalPage from '../views/RegionalPage.vue'
import RegistrationPage from '../components/RegistrationPage.vue'
import ProfilePage from '../views/ProfilePage.vue'
import ProfileUpdate from '../components/ProfileUpdate.vue'
import ReferralCard from '../components/ReferralCard.vue'
import ReferralDetailsPage from '../components/ReferralDetailsPage.vue'
import PaymentPage from '../components/Payment.vue'
import WalletDashboard from '@/components/wallet/WalletDashboard.vue'
import store from '../store'
import CategoryVideos from '../views/CategoryVideos.vue'
import WatchList from '@/views/WatchList.vue'
import PrivacyPolicy from '../views/PrivacyPolicy.vue'
import TermsOfService from '../views/TermsOfService.vue'
import CookiePolicy from '../views/CookiePolicy.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginPage
  },
  {
    path: '/register',
    name: 'Register',
    component: RegistrationPage
  },
  {
    path: '/plans',
    name: 'SubscriptionPlans',
    component: SubscriptionPlans
  },
  {
    path: '/player',
    name: 'VideoPlayer',
    component: VideoPlayer
  },
  {
    path: '/movie/:id',
    name: 'MovieDetails',
    component: MovieDetails,
    props: true
  },
  {
    path: '/regional',
    name: 'RegionalContent',
    component: RegionalContent
  },
  {
    path: '/search',
    name: 'Search',
    component: SearchPage
  },
  {
    path: '/regional/:stateId',
    name: 'RegionalState',
    component: RegionalPage
  },
  {
    path: '/profile',
    name: 'Profile',
    component: ProfilePage,
    meta: { requiresAuth: true }
  },
  {
    path: '/profile/update',
    name: 'ProfileUpdate',
    component: ProfileUpdate,
    meta: { requiresAuth: true }
  },
  {
    path: '/referral',
    name: 'Referral',
    component: ReferralCard
  },
  {
    path: '/referral/details',
    name: 'ReferralDetails',
    component: ReferralDetailsPage
  },
  {
    path: '/downloads',
    name: 'Downloads',
    component: () => import('../views/Downloads.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/payment',
    name: 'Payment',
    component: PaymentPage,
    meta: { requiresAuth: true }
  },
  {
    path: '/wallet',
    name: 'Wallet',
    component: WalletDashboard,
    meta: {
      requiresAuth: true,
      title: 'Wallet'
    }
  },
  {
    path: '/category/:id',
    name: 'CategoryVideos',
    component: CategoryVideos
  },
  {
    path: '/watchlist',
    name: 'WatchList',
    component: WatchList,
    meta: { requiresAuth: true }
  },
  {
    path: '/privacy-policy',
    name: 'PrivacyPolicy',
    component: PrivacyPolicy
  },
  {
    path: '/terms-of-service',
    name: 'TermsOfService',
    component: TermsOfService
  },
  {
    path: '/cookie-policy',
    name: 'CookiePolicy',
    component: CookiePolicy
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('../views/NotFound.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    // always scroll to top when navigating to a new route
    return { top: 0 }
  }
})

router.beforeEach((to, from, next) => {
  // Check both Vuex store and localStorage
  const isAuthenticated = store.state.isAuthenticated || localStorage.getItem('isAuthenticated') === 'true'
  const user = store.state.user || JSON.parse(localStorage.getItem('user'))
  
  // Debug log
  console.log('Navigation:', { 
    from: from.path,
    to: to.path,
    isAuthenticated,
    hasUser: !!user
  })
  
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!isAuthenticated || !user) {
      // Save attempted destination
      const redirect = to.fullPath
      console.log('Auth required - Redirecting to login, saving redirect:', redirect)
      next({
        path: '/login',
        query: { redirect }
      })
    } else {
      console.log('Auth check passed - Proceeding to', to.path)
      next()
    }
  } else if (to.path === '/login' && isAuthenticated && user) {
    // If user is already logged in and tries to access login page
    const redirect = to.query.redirect || '/'
    console.log('Already logged in - Redirecting to:', redirect)
    next(redirect)
  } else {
    console.log('No auth required - Proceeding to', to.path)
    next()
  }
})

export default router 