  },
  computed: {
    hasSelection() {
      return this.selectedState || this.selectedDistrict || 
             this.selectedConstituency || this.selectedMandal || 
             this.selectedVillage;
    },
    breadcrumbTrail() {
      const trail = [];
      if (this.selectedState) {
        trail.push({ name: this.selectedState.name, type: 'state' });
      }
      if (this.selectedDistrict) {
        trail.push({ name: this.selectedDistrict.name, type: 'district' });
      }
      if (this.selectedConstituency) {
        trail.push({ name: this.selectedConstituency.name, type: 'constituency' });
      }
      if (this.selectedMandal) {
        trail.push({ name: this.selectedMandal.name, type: 'mandal' });
      }
      if (this.selectedVillage) {
        trail.push({ name: this.selectedVillage.name, type: 'village' });
      }
      return trail;
    },
    currentLevelItems() {
      if (this.loading) return [];
      if (!this.selectedState) return this.states;
      if (!this.selectedDistrict && this.districts.length > 0) return this.districts;
      if (!this.selectedConstituency && this.constituencies.length > 0) return this.constituencies;
      if (!this.selectedMandal && this.mandals.length > 0) return this.mandals;
      if (this.villages.length > 0) return this.villages;
      return [];
    },
    getNextLevelName() {
      if (!this.selectedState) return 'districts'
      if (!this.selectedDistrict) return 'constituencies'
      if (!this.selectedConstituency) return 'mandals'
      if (!this.selectedMandal) return 'villages'
      return 'videos'
    },
    filteredVideos() {
      if (!this.selectedVillage) return [];
      let videos = this.selectedVillage.videos;
      
      // Apply category filter
      if (this.activeCategory.id !== 'all') {
        videos = videos.filter(v => v.category === this.activeCategory.id);
      }
      
      // Apply quick filters
      if (this.quickFilters.find(f => f.id === 'hd' && f.active)) {
        videos = videos.filter(v => v.quality === 'HD' || v.quality === '4K');
      }
      if (this.quickFilters.find(f => f.id === 'latest' && f.active)) {
        videos = videos.sort((a, b) => new Date(b.date) - new Date(a.date));
      }
      
      return videos;
    },
    getLocationPath() {
      return this.breadcrumbTrail.map(item => item.name).join(' > ');
    },
    getMapTitle() {
      if (!this.selectedState) return 'Explore India'
      if (!this.selectedDistrict) return this.selectedState.name
      return this.selectedDistrict.name
    },
    getMapDescription() {
      if (!this.selectedState) return 'Select a state to explore regional content'
      if (!this.selectedDistrict) return `Explore districts in ${this.selectedState.name}`
      return `Explore areas in ${this.selectedDistrict.name}`
    },
    getMapStats() {
      if (!this.selectedState) {
        return {
          title: 'India Statistics',
          stats: [
            { label: 'States', value: this.states.length },
            { label: 'Total Districts', value: this.states.reduce((acc, state) => acc + (state.districts_count || 0), 0) },
            { label: 'Total Videos', value: '2,500+' }
          ]
        };
      }
      
      if (!this.selectedDistrict) {
        return {
          title: `${this.selectedState.name} Statistics`,
          stats: [
            { 
              label: 'Districts', 
              value: this.selectedState.districts_count || this.districts.length || 0 
            },
            { 
              label: 'Constituencies', 
              value: this.selectedState.constituencies_count || 
                     this.districts.reduce((acc, district) => acc + (district.constituencies?.length || 0), 0) || 0 
            },
            { 
              label: 'Mandals', 
              value: this.selectedState.mandals_count || 
                     this.districts.reduce((acc, district) => 
                       acc + district.constituencies?.reduce((acc2, constituency) => 
                         acc2 + (constituency.mandals?.length || 0), 0) || 0, 0) || 0 
            },
            { 
              label: 'Villages', 
              value: this.selectedState.villages_count || 
                     this.districts.reduce((acc, district) => 
                       acc + district.constituencies?.reduce((acc2, constituency) => 
                         acc2 + constituency.mandals?.reduce((acc3, mandal) => 
                           acc3 + (mandal.villages?.length || 0), 0) || 0, 0) || 0, 0) || 0 
            }
          ]
        };
      }

      return {
        title: `${this.selectedDistrict.name} Statistics`,
        stats: [
          { 
            label: 'Constituencies', 
            value: this.selectedDistrict.constituencies?.length || 0 
          },
          { 
            label: 'Mandals', 
            value: this.selectedDistrict.constituencies?.reduce((acc, constituency) => 
              acc + (constituency.mandals?.length || 0), 0) || 0 
          },
          { 
            label: 'Villages', 
            value: this.selectedDistrict.constituencies?.reduce((acc, constituency) => 
              acc + constituency.mandals?.reduce((acc2, mandal) => 
                acc2 + (mandal.villages?.length || 0), 0) || 0, 0) || 0 
          }
        ]
      };
    },
    getListTitle() {
      if (!this.selectedState) return 'States'
      if (!this.selectedDistrict) return 'Districts'
      if (!this.selectedConstituency) return 'Constituencies'
      if (!this.selectedMandal) return 'Mandals'
      return 'Villages'
    },
    currentLevelVideos() {
      if (this.selectedVillage && this.selectedVillage.videos) {
        return this.selectedVillage.videos;
      }
      if (this.selectedMandal) {
        const videos = [];
        const district = this.selectedState?.districts?.find(d => d.id === this.selectedDistrict.id);
        const constituency = district?.constituencies?.find(c => c.id === this.selectedConstituency.id);
        const mandal = constituency?.mandals?.find(m => m.id === this.selectedMandal.id);
        mandal?.villages?.forEach(village => {
          if (village.videos) {
            videos.push(...village.videos);
          }
        });
        return videos;
      }
      if (this.selectedConstituency) {
        const videos = [];
        const district = this.selectedState?.districts?.find(d => d.id === this.selectedDistrict.id);
        const constituency = district?.constituencies?.find(c => c.id === this.selectedConstituency.id);
        constituency?.mandals?.forEach(mandal => {
          mandal.villages?.forEach(village => {
            if (village.videos) {
              videos.push(...village.videos);
            }
          });
        });
        return videos;
      }
      if (this.selectedDistrict) {
        const videos = [];
        const district = this.selectedState?.districts?.find(d => d.id === this.selectedDistrict.id);
        district?.constituencies?.forEach(constituency => {
          constituency.mandals?.forEach(mandal => {
            mandal.villages?.forEach(village => {
              if (village.videos) {
                videos.push(...village.videos);
              }
            });
          });
        });
        return videos;
      }
      if (this.selectedState) {
        const videos = [];
        this.selectedState.districts?.forEach(district => {
          district.constituencies?.forEach(constituency => {
            constituency.mandals?.forEach(mandal => {
              mandal.villages?.forEach(village => {
                if (village.videos) {
                  videos.push(...village.videos);
                }
              });
            });
          });
        });
        return videos;
      }
      return [];
    },
    paginatedVideos() {
      const startIndex = 0;
      const endIndex = this.currentVideoPage * this.videosPerPage;
      return this.currentLevelVideos.slice(startIndex, endIndex);
    },
    hasMoreVideos() {
      return this.currentLevelVideos.length > this.paginatedVideos.length;
    },
    uniqueCategories() {
      const categories = new Set();
      this.currentLevelVideos.forEach(video => {
        if (video.category) categories.add(video.category);
      });
      return Array.from(categories);
    },
    filteredLocations() {
      return this.searchResults;
    },
    searchPlaceholder() {
      return "Search states, districts, constituencies, mandals, or villages...";
    },
    getNavigateBackTitle() {
      if (this.showNavigateBackConfirm === 'state') return 'state';
      if (this.showNavigateBackConfirm === 'district') return 'district';
      if (this.showNavigateBackConfirm === 'constituency') return 'constituency';
      if (this.showNavigateBackConfirm === 'mandal') return 'mandal';
      return 'village';
    }
  },
  methods: {
    async fetchStates() {
      try {
        const response = await api.get('frontend/states/')
        console.log('States response:', response.data)
        if (response.data.status === 'success') {
          this.states = response.data.data
          this.navigationLevels[0].count = this.states.length
        }
      } catch (err) {
        console.error('Error fetching states:', err)
        this.error = 'Error loading states. Please try again later.'
      }
    },
    async fetchDistricts(stateId) {
      try {
        console.log('Fetching districts for state ID:', stateId);
        const response = await api.get('frontend/districts/', {
          params: { state_id: stateId }
        });
        
        if (response.data && response.data.status === 'success' && Array.isArray(response.data.data)) {
          // Process districts to include constituency, mandal and village counts
          this.districts = await Promise.all(response.data.data.map(async district => {
            // Fetch constituencies for this district
            const constituenciesResponse = await api.get('frontend/constituencies/', {
              params: { district_id: district.id }
            });
            
            if (constituenciesResponse.data.status === 'success') {
              district.constituencies = await Promise.all(constituenciesResponse.data.data.map(async constituency => {
                // Fetch mandals for this constituency
                const mandalsResponse = await api.get('frontend/mandals/', {
                  params: { constituency_id: constituency.id }
                });
                
                if (mandalsResponse.data.status === 'success') {
                  constituency.mandals = await Promise.all(mandalsResponse.data.data.map(async mandal => {
                    // Fetch villages for this mandal
                    const villagesResponse = await api.get('frontend/villages/', {
                      params: { mandal_id: mandal.id }
                    });
                    
                    if (villagesResponse.data.status === 'success') {
                      mandal.villages = villagesResponse.data.data;
                    }
                    return mandal;
                  }));
                }
                return constituency;
              }));
            }
            return district;
          }));

          this.navigationLevels[1].count = this.districts.length;
          console.log('Districts loaded with full hierarchy:', this.districts);
        } else {
          throw new Error('Invalid response format from server');
        }
      } catch (err) {
        console.error('Error details:', err.response || err);
        this.error = err.response?.data?.message || 'Error loading districts. Please try again later.';
        this.districts = [];
        this.navigationLevels[1].count = 0;
      }
    },
    async fetchConstituencies(districtId) {
      try {
        const response = await api.get(`frontend/constituencies/`, {
          params: { district_id: districtId }
        });
        if (response.data.status === 'success') {
          this.constituencies = await Promise.all(response.data.data.map(async constituency => {
            // Fetch mandals for this constituency
            const mandalsResponse = await api.get('frontend/mandals/', {
              params: { constituency_id: constituency.id }
            });
            
            if (mandalsResponse.data.status === 'success') {
              constituency.mandals = await Promise.all(mandalsResponse.data.data.map(async mandal => {
                // Fetch villages for this mandal
                const villagesResponse = await api.get('frontend/villages/', {
                  params: { mandal_id: mandal.id }
                });
                
                if (villagesResponse.data.status === 'success') {
                  mandal.villages = villagesResponse.data.data;
                }
                return mandal;
              }));
            }
            return constituency;
          }));
          
          this.navigationLevels[2].count = this.constituencies.length;
        }
      } catch (err) {
        console.error('Error fetching constituencies:', err);
        this.error = 'Error loading constituencies. Please try again later.';
      }
    },
    async fetchMandals(constituencyId) {
      try {
        const response = await api.get(`frontend/mandals/`, {
          params: {
            constituency_id: constituencyId
          }
        });
        if (response.data.status === 'success') {
          // Process each mandal to ensure village count is available
          this.mandals = response.data.data.map(mandal => ({
            ...mandal,
            villages_count: mandal.villages?.length || mandal.villages_count || 0
          }));
          this.navigationLevels[3].count = this.mandals.length;
          console.log('Mandals loaded with village counts:', this.mandals);
        }
      } catch (err) {
        console.error('Error fetching mandals:', err);
        this.error = 'Error loading mandals. Please try again later.';
      }
    },
    async fetchVillages(mandalId) {
      try {
        const response = await api.get(`frontend/villages/`, {
          params: {
            mandal_id: mandalId
          }
        })
        if (response.data.status === 'success') {
          this.villages = response.data.data
          this.navigationLevels[4].count = this.villages.length
        }
      } catch (err) {
        console.error('Error fetching villages:', err)
        this.error = 'Error loading villages. Please try again later.'
      }
    },
    async selectState(state) {
      try {
        this.loading = true;
        this.error = null;
        this.selectedState = state;
        this.selectedDistrict = null;
        this.selectedConstituency = null;
        this.selectedMandal = null;
        this.selectedVillage = null;
        
        console.log('Selected state:', state);

        // Fetch state statistics
        try {
          const statsResponse = await api.get(`frontend/state-statistics/${state.id}/`);
          if (statsResponse.data && statsResponse.data.status === 'success') {
            const stats = statsResponse.data.data;
            this.selectedState = {
              ...state,
              districts_count: stats.districts_count || 0,
              constituencies_count: stats.constituencies_count || 0,
              mandals_count: stats.mandals_count || 0,
              villages_count: stats.villages_count || 0
            };
          }
        } catch (statsErr) {
          console.error('Error fetching state statistics:', statsErr);
        }

        // Fetch districts
        const response = await api.get('frontend/districts/', {
          params: { state_id: state.id }
        });
        
        if (response.data && response.data.status === 'success' && Array.isArray(response.data.data)) {
          this.districts = response.data.data;
          this.navigationLevels[1].count = this.districts.length;

          // Update state statistics if API stats weren't available
          if (!this.selectedState.districts_count) {
            this.selectedState.districts_count = this.districts.length;
          }
        } else {
          throw new Error('Invalid response format from server');
        }
      } catch (err) {
        console.error('Error details:', err.response || err);
        this.error = err.response?.data?.message || err.message || 'Error loading districts. Please try again later.';
        this.districts = [];
        this.navigationLevels[1].count = 0;
      } finally {
        this.loading = false;
      }
    },
    async selectDistrict(district) {
      this.selectedDistrict = district
      this.selectedConstituency = null
      this.selectedMandal = null
      this.selectedVillage = null
      await this.fetchConstituencies(district.id)
    },
    async selectConstituency(constituency) {
      this.selectedConstituency = constituency
      this.selectedMandal = null
      this.selectedVillage = null
      await this.fetchMandals(constituency.id)
    },
    async selectMandal(mandal) {
      this.selectedMandal = mandal
      this.selectedVillage = null
      await this.fetchVillages(mandal.id)
    },
    selectVillage(village) {
      this.selectedVillage = village
    },
    setActiveLevel(level) {
      this.activeLevel = level
      if (level.id === 'state') {
        this.clearAll()
      }
    },
    async clearAll() {
      try {
        this.loading = true;
        // Clear all selections
        this.selectedState = null;
        this.selectedDistrict = null;
        this.selectedConstituency = null;
        this.selectedMandal = null;
        this.selectedVillage = null;
        this.districts = [];
        this.constituencies = [];
        this.mandals = [];
        this.villages = [];
        // Fetch fresh states data
        await this.fetchStates();
      } catch (error) {
        console.error('Error during clear:', error);
        this.error = 'Error loading states. Please try again.';
      } finally {
        this.loading = false;
      }
    },
    async confirmClear() {
      this.showClearConfirm = false;
      await this.clearAll();
    },
    getIconForLevel(levelId) {
      const level = this.navigationLevels.find(l => l.id === levelId);
      return level ? level.icon : 'fa-map-marker-alt';
    },
    async handleLocationSearch() {
      // Clear previous timeout
      if (this.searchTimeout) {
        clearTimeout(this.searchTimeout);
      }

      // Show loading state
      this.isSearching = true;
      this.showLocationDropdown = true;

      // Set new timeout for debouncing
      this.searchTimeout = setTimeout(async () => {
        try {
          if (!this.searchQuery.trim()) {
            this.searchResults = [];
            this.isSearching = false;
            return;
          }

          console.log('Performing search for:', this.searchQuery.trim());
          
          // Ensure districts are loaded for all states
          for (const state of this.states) {
            if (!state.districts) {
              console.log('Loading districts for state:', state.name);
              await this.fetchDistricts(state.id);
              state.districts = this.districts;
            }
          }

          // Search in all levels
          const results = await this.performSearch(this.searchQuery.trim());
          this.searchResults = results;
        } catch (error) {
          console.error('Search error:', error);
          this.error = 'Error performing search. Please try again.';
        } finally {
          this.isSearching = false;
        }
      }, this.searchDebounceTime);
    },

    async performSearch(query) {
      const results = [];
      const searchTerms = query.toLowerCase().split(' ').filter(term => term.length > 0);

      console.log('Starting search with terms:', searchTerms);
      console.log('Current states data:', this.states);

      // Helper function to calculate relevance score
      const calculateRelevanceScore = (text, searchTerms) => {
        const lowerText = text.toLowerCase();
        let score = 0;
        
        // Exact match gets highest score
        if (lowerText === query.toLowerCase()) {
          score += 100;
        }
        
        // Partial matches
        searchTerms.forEach(term => {
          if (lowerText.includes(term)) {
            score += 10;
            // Bonus for matches at the start of words
            if (lowerText.startsWith(term)) {
              score += 5;
            }
          }
        });
        
        return score;
      };

      // Search through states
      for (const state of this.states) {
        console.log('Searching in state:', state.name);
        
        const stateScore = calculateRelevanceScore(state.name, searchTerms);
        if (stateScore > 0) {
          results.push({
            id: state.id,
            name: state.name,
            type: 'state',
            score: stateScore,
            parentInfo: {}
          });
        }

        // Search through districts
        if (state.districts) {
          console.log('Districts in state:', state.name, state.districts);
          
          for (const district of state.districts) {
            console.log('Checking district:', district.name);
            
            const districtScore = calculateRelevanceScore(district.name, searchTerms);
            if (districtScore > 0) {
              console.log('Found matching district:', district.name, 'with score:', districtScore);
              
              results.push({
                id: district.id,
                name: district.name,
                type: 'district',
                score: districtScore,
                parentInfo: { state: state.name }
              });
            }

            // Search through constituencies
            if (district.constituencies) {
              for (const constituency of district.constituencies) {
                const constituencyScore = calculateRelevanceScore(constituency.name, searchTerms);
                if (constituencyScore > 0) {
                  results.push({
                    id: constituency.id,
                    name: constituency.name,
                    type: 'constituency',
                    score: constituencyScore,
                    parentInfo: {
                      state: state.name,
                      district: district.name
                    }
                  });
                }

                // Search through mandals
                if (constituency.mandals) {
                  for (const mandal of constituency.mandals) {
                    const mandalScore = calculateRelevanceScore(mandal.name, searchTerms);
                    if (mandalScore > 0) {
                      results.push({
                        id: mandal.id,
                        name: mandal.name,
                        type: 'mandal',
                        score: mandalScore,
                        parentInfo: {
                          state: state.name,
                          district: district.name,
                          constituency: constituency.name
                        }
                      });
                    }

                    // Search through villages
                    if (mandal.villages) {
                      for (const village of mandal.villages) {
                        const villageScore = calculateRelevanceScore(village.name, searchTerms);
                        if (villageScore > 0) {
                          results.push({
                            id: village.id,
                            name: village.name,
                            type: 'village',
                            score: villageScore,
                            parentInfo: {
                              state: state.name,
                              district: district.name,
                              constituency: constituency.name,
                              mandal: mandal.name
                            }
                          });
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      console.log('Search results:', results);

      // Sort results by score and type
      const sortedResults = results.sort((a, b) => {
        if (b.score !== a.score) {
          return b.score - a.score;
        }
        const typeOrder = { state: 0, district: 1, constituency: 2, mandal: 3, village: 4 };
        return typeOrder[a.type] - typeOrder[b.type];
      });

      console.log('Sorted results:', sortedResults);
      return sortedResults;
    },
    async selectLocation(location) {
      this.showLocationDropdown = false;
      this.searchQuery = location.name;

      // Clear selections up to the selected level
      if (location.type === 'state') {
        this.clearAll();
        const state = this.states.find(s => s.name === location.name);
        if (state) this.selectState(state);
      } else if (location.type === 'district') {
        this.selectedConstituency = null;
        this.selectedMandal = null;
        this.selectedVillage = null;
        const state = this.states.find(s => s.name === location.parentInfo.state);
        if (state) {
          this.selectState(state);
          const district = state.districts.find(d => d.name === location.name);
          if (district) this.selectDistrict(district);
        }
      } else if (location.type === 'constituency') {
        this.selectedMandal = null;
        this.selectedVillage = null;
        const state = this.states.find(s => s.name === location.parentInfo.state);
        if (state) {
          this.selectState(state);
          const district = state.districts.find(d => d.name === location.parentInfo.district);
          if (district) {
            this.selectDistrict(district);
            const constituency = district.constituencies.find(c => c.name === location.name);
            if (constituency) this.selectConstituency(constituency);
          }
        }
      } else if (location.type === 'mandal') {
        this.selectedVillage = null;
        const state = this.states.find(s => s.name === location.parentInfo.state);
        if (state) {
          this.selectState(state);
          const district = state.districts.find(d => d.name === location.parentInfo.district);
          if (district) {
            this.selectDistrict(district);
            const constituency = district.constituencies.find(c => c.name === location.parentInfo.constituency);
            if (constituency) {
              this.selectConstituency(constituency);
              const mandal = constituency.mandals.find(m => m.name === location.name);
              if (mandal) this.selectMandal(mandal);
            }
          }
        }
      } else if (location.type === 'village') {
        const state = this.states.find(s => s.name === location.parentInfo.state);
        if (state) {
          this.selectState(state);
          const district = state.districts.find(d => d.name === location.parentInfo.district);
          if (district) {
            this.selectDistrict(district);
            const constituency = district.constituencies.find(c => c.name === location.parentInfo.constituency);
            if (constituency) {
              this.selectConstituency(constituency);
              const mandal = constituency.mandals.find(m => m.name === location.parentInfo.mandal);
              if (mandal) {
                this.selectMandal(mandal);
                const village = mandal.villages.find(v => v.name === location.name);
                if (village) this.selectVillage(village);
              }
            }
          }
        }
      }
    },

    async selectItem(item) {
      if (!item) return;
      
      if (!this.selectedState) {
        await this.selectState(item);
      } else if (!this.selectedDistrict) {
        if (this.districts.find(d => d.id === item.id)) {
          await this.selectDistrict(item);
        }
      } else if (!this.selectedConstituency) {
        if (this.constituencies.find(c => c.id === item.id)) {
          await this.selectConstituency(item);
        }
      } else if (!this.selectedMandal) {
        if (this.mandals.find(m => m.id === item.id)) {
          await this.selectMandal(item);
        }
      } else {
        if (this.villages.find(v => v.id === item.id)) {
          await this.selectVillage(item);
        }
      }
    },
    toggleFavorite() {
      this.isFavorite = !this.isFavorite;
    },
    setActiveCategory(category) {
      this.activeCategory = category;
    },
    applyFilter(filter) {
      filter.active = !filter.active;
    },
    loadMoreVideos() {
      this.currentVideoPage++;
    },
    resetVideoPagination() {
      this.currentVideoPage = 1;
    },
    filterByTopic(topic) {
      topic.active = !topic.active;
      // Filter videos based on active topics
      this.currentVideoPage = 1; // Reset pagination
    },
    filterByDate(type) {
      if (type === 'latest') {
        this.currentLevelVideos.sort((a, b) => {
          return new Date(b.date) - new Date(a.date);
        });
      }
      this.currentVideoPage = 1; // Reset pagination
    },
    filterByViews() {
      this.currentLevelVideos.sort((a, b) => {
        const aViews = parseInt(a.views.replace('K', '000'));
        const bViews = parseInt(b.views.replace('K', '000'));
        return bViews - aViews;
      });
      this.currentVideoPage = 1; // Reset pagination
    },
    filterByQuality(quality) {
      this.quickFilters.forEach(filter => {
        if (filter.id === 'hd') {
          filter.active = quality === 'HD';
        }
      });
      this.currentVideoPage = 1; // Reset pagination
    },
    showSavedItems() {
      // Toggle saved items view
      this.quickNavigationActions.find(action => action.id === 4).active = 
        !this.quickNavigationActions.find(action => action.id === 4).active;
      this.currentVideoPage = 1; // Reset pagination
    },
    async useCurrentLocation() {
      this.isLoadingLocation = true;
      this.locationError = null;
      this.userLocation = null;
      this.locationMethod = null;

      try {
        // Try HTML5 Geolocation first
        if (navigator.geolocation) {
          try {
            const position = await this.getGeolocationPosition();
            this.userLocation = {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            };
            this.locationMethod = 'gps';
          } catch (geoError) {
            console.warn('Geolocation failed:', geoError);
            // Fall back to IP-based location
            await this.getIPBasedLocation();
          }
        } else {
          // If geolocation is not supported, try IP-based location
          await this.getIPBasedLocation();
        }

        // If we have a location, find nearest village
        if (this.userLocation) {
          const nearestVillage = await this.findNearestVillage(this.userLocation);
          if (nearestVillage) {
            await this.navigateToLocation(nearestVillage);
            this.showLocationSuccess(nearestVillage.village);
          } else {
            throw new Error('No nearby locations found in our database');
          }
        } else {
          throw new Error('Could not determine your location');
        }
      } catch (error) {
        this.handleLocationError(error);
      } finally {
        this.isLoadingLocation = false;
      }
    },

    getGeolocationPosition() {
      return new Promise((resolve, reject) => {
        const options = {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 0
        };

        navigator.geolocation.getCurrentPosition(resolve, reject, options);
      });
    },

    async getIPBasedLocation() {
      try {
        // Using ipapi.co for IP-based location (free tier, no API key needed)
        const response = await fetch('https://ipapi.co/json/');
        if (!response.ok) throw new Error('IP location service failed');
        
        const data = await response.json();
        this.userLocation = {
          lat: parseFloat(data.latitude),
          lng: parseFloat(data.longitude)
        };
        this.locationMethod = 'ip';
      } catch (error) {
        console.error('IP location failed:', error);
        throw new Error('Could not determine location from IP');
      }
    },

    async findNearestVillage(location) {
      let nearestVillage = null;
      let shortestDistance = Infinity;

      // Calculate distances to all villages
      this.locations.forEach(village => {
        const distance = this.calculateDistance(
          location.lat,
          location.lng,
          village.coordinates.lat,
          village.coordinates.lng
        );

        if (distance < shortestDistance) {
          shortestDistance = distance;
          nearestVillage = village;
        }
      });

      // Return the nearest village if it's within 50km
      return shortestDistance <= 50 ? nearestVillage : null;
    },

    async navigateToLocation(location) {
      // Clear previous selections
      this.clearAll();

      // Navigate through hierarchy
      const state = this.states.find(s => s.name === location.state);
      if (!state) throw new Error('State not found');
      await this.selectState(state);

      const district = state.districts.find(d => d.name === location.district);
      if (!district) throw new Error('District not found');
      await this.selectDistrict(district);

      const constituency = district.constituencies.find(c => c.name === location.constituency);
      if (!constituency) throw new Error('Constituency not found');
      await this.selectConstituency(constituency);

      const mandal = constituency.mandals.find(m => m.name === location.mandal);
      if (!mandal) throw new Error('Mandal not found');
      await this.selectMandal(mandal);

      const village = mandal.villages.find(v => v.name === location.village);
      if (!village) throw new Error('Village not found');
      await this.selectVillage(village);
    },

    showLocationSuccess(villageName) {
      // You can implement a toast/notification system here
      console.log(`Located near ${villageName}`);
    },

    handleLocationError(error) {
      this.locationError = error.message;
      console.error('Location error:', error);
      
      // Show user-friendly error message
      let errorMessage = 'Could not determine your location. ';
      if (error.code === 1) {
        errorMessage += 'Please allow location access and try again.';
      } else if (error.code === 2) {
        errorMessage += 'Location service is not available.';
      } else if (error.code === 3) {
        errorMessage += 'Location request timed out.';
      }
      
      // You can implement a toast/notification system here
      console.error(errorMessage);
    },

    calculateDistance(lat1, lon1, lat2, lon2) {
      const R = 6371; // Earth's radius in kilometers
      const dLat = this.deg2rad(lat2 - lat1);
      const dLon = this.deg2rad(lon2 - lon1);
      const a = 
        Math.sin(dLat/2) * Math.sin(dLat/2) +
        Math.cos(this.deg2rad(lat1)) * Math.cos(this.deg2rad(lat2)) * 
        Math.sin(dLon/2) * Math.sin(dLon/2);
      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
      return R * c;
    },

    deg2rad(deg) {
      return deg * (Math.PI/180);
    },

    loadVillageVideos(village) {
      // Reset video pagination
      this.currentVideoPage = 1;
      
      // Update active filters if any
      this.updateActiveFilters();
      
      // Update current videos based on village data
      if (village && village.videos) {
        this.currentLevelVideos = village.videos;
      }
      
      // Scroll to videos section
      this.$nextTick(() => {
        const videosSection = document.querySelector('.videos-section');
        if (videosSection) {
          videosSection.scrollIntoView({ behavior: 'smooth' });
        }
      });
    },

    updateActiveFilters() {
      // Reset all filters
      this.quickFilters.forEach(filter => filter.active = false);
      this.trendingTopics.forEach(topic => topic.active = false);
      this.activeCategory = { id: 'all', name: 'All Videos' };
    },

    delay(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },

    async selectVillageWithDelay(village) {
      await this.delay(300);
      this.selectedVillage = village;
      // Load videos for the selected village
      this.loadVillageVideos(village);
    },
    async navigateToLevel(index) {
      // Clear selections after the clicked level
      if (index === 0) {
        // If clicking on state level, clear everything
        this.clearAll();
        await this.fetchStates();
      } else if (index === 1) {
        // If clicking on district level, clear from constituency onwards
        this.selectedConstituency = null;
        this.selectedMandal = null;
        this.selectedVillage = null;
        this.constituencies = [];
        this.mandals = [];
        this.villages = [];
        // Refetch districts for the current state
        if (this.selectedState) {
          await this.fetchDistricts(this.selectedState.id);
        }
      } else if (index === 2) {
        // If clicking on constituency level, clear from mandal onwards
        this.selectedMandal = null;
        this.selectedVillage = null;
        this.mandals = [];
        this.villages = [];
        // Refetch constituencies for the current district
        if (this.selectedDistrict) {
          await this.fetchConstituencies(this.selectedDistrict.id);
        }
      } else if (index === 3) {
        // If clicking on mandal level, clear village
        this.selectedVillage = null;
        this.villages = [];
        // Refetch mandals for the current constituency
        if (this.selectedConstituency) {
          await this.fetchMandals(this.selectedConstituency.id);
        }
      }
    },
    showNavigateConfirm(index) {
      this.showNavigateBackConfirm = this.breadcrumbTrail[index].type;
      this.navigateBackIndex = index; // Store the index for later use
    },
    async confirmNavigateBack() {
      try {
        this.loading = true;
        const index = this.navigateBackIndex;
        this.showNavigateBackConfirm = null;
        
        // Navigate back based on the level
        if (index === 0) {
          // Going back to state level
          await this.clearAll();
        } else if (index === 1) {
          // Going back to district level
          this.selectedConstituency = null;
          this.selectedMandal = null;
          this.selectedVillage = null;
          this.constituencies = [];
          this.mandals = [];
          this.villages = [];
          if (this.selectedState && this.selectedDistrict) {
            await this.fetchDistricts(this.selectedState.id);
          }
        } else if (index === 2) {
          // Going back to constituency level
          this.selectedMandal = null;
          this.selectedVillage = null;
          this.mandals = [];
          this.villages = [];
          if (this.selectedDistrict) {
            await this.fetchConstituencies(this.selectedDistrict.id);
          }
        } else if (index === 3) {
          // Going back to mandal level
          this.selectedVillage = null;
          this.villages = [];
          if (this.selectedConstituency) {
            await this.fetchMandals(this.selectedConstituency.id);
          }
        }
      } catch (error) {
        console.error('Error during navigation:', error);
        this.error = 'Error loading data. Please try again.';
      } finally {
        this.loading = false;
      }
    },
    getVillageCount(item) {
      // If villages array exists, return its length
      if (Array.isArray(item.villages)) {
        return item.villages.length;
      }
      // If villages_count exists, return that
      if (typeof item.villages_count === 'number') {
        return item.villages_count;
      }
      // Default to 0 if no data available
      return 0;
    }
  },
  watch: {
    // Add watchers for smooth transitions
    selectedState(newVal) {
      if (newVal) {
        this.$emit('hierarchy-updated', { level: 'state', value: newVal });
      }
    },
    selectedDistrict(newVal) {
      if (newVal) {
        this.$emit('hierarchy-updated', { level: 'district', value: newVal });
      }
    },
    selectedConstituency(newVal) {
      if (newVal) {
        this.$emit('hierarchy-updated', { level: 'constituency', value: newVal });
      }
    },
    selectedMandal(newVal) {
      if (newVal) {
        this.$emit('hierarchy-updated', { level: 'mandal', value: newVal });
      }
    },
    selectedVillage(newVal) {
      if (newVal) {
        this.$emit('hierarchy-updated', { level: 'village', value: newVal });
        this.loadVillageVideos(newVal);
      }
    }
  },
  mounted() {
    // Close dropdown when clicking outside
    this.handleOutsideClick = (e) => {
      if (!e.target.closest('.relative')) {
        this.showLocationDropdown = false;
      }
    };
    document.addEventListener('click', this.handleOutsideClick);
  },
  beforeUnmount() {
    // Clean up event listener
    document.removeEventListener('click', this.handleOutsideClick);
  },
  async created() {
    await this.fetchStates()
    
    // Check if state_id is present in query parameters
    const stateId = this.$route.query.state_id
    if (stateId) {
      const state = this.states.find(s => s.id === parseInt(stateId))
      if (state) {
        await this.selectState(state)
      }
    }
  }
}
</script>

