import { createStore } from 'vuex'

// Initial state
const getDefaultState = () => {
  return {
    user: JSON.parse(localStorage.getItem('user')) || null,
    isAuthenticated: localStorage.getItem('isAuthenticated') === 'true',
    token: localStorage.getItem('token') || null,
    subscription: null,
    isLoading: false,
    toast: {
      show: false,
      message: '',
      type: 'info', // 'success', 'error', 'info', 'warning'
      duration: 3000
    },
    homePageData: null
  };
};

export default createStore({
  state: getDefaultState(),
  mutations: {
    setUser(state, user) {
      state.user = user;
      state.isAuthenticated = !!user;
      // Sync with localStorage
      localStorage.setItem('user', JSON.stringify(user));
      localStorage.setItem('isAuthenticated', 'true');
    },
    setAuthenticated(state, value) {
      state.isAuthenticated = value;
      localStorage.setItem('isAuthenticated', value.toString());
    },
    setToken(state, token) {
      state.token = token;
      if (token) {
        localStorage.setItem('token', token);
      } else {
        localStorage.removeItem('token');
      }
    },
    setLoading(state, value) {
      state.isLoading = value;
    },
    setSubscription(state, subscription) {
      state.subscription = subscription;
    },
    logout(state) {
      Object.assign(state, getDefaultState());
      localStorage.removeItem('user');
      localStorage.removeItem('isAuthenticated');
      localStorage.removeItem('token');
      localStorage.removeItem('subscription');
    },
    RESET_STATE(state) {
      Object.assign(state, getDefaultState());
    },
    showToast(state, { message, type = 'info', duration = 3000 }) {
      state.toast = {
        show: true,
        message,
        type,
        duration
      };
    },
    hideToast(state) {
      state.toast.show = false;
    },
    setHomePageData(state, data) {
      state.homePageData = data
    },
    clearHomePageData(state) {
      state.homePageData = null
    }
  },
  actions: {
    initializeStore({ commit }) {
      // Load user data from localStorage on app start
      const user = JSON.parse(localStorage.getItem('user'));
      const token = localStorage.getItem('token');
      if (user && token) {
        commit('setUser', user);
        commit('setToken', token);
      }
    },
    logout({ commit }) {
      commit('logout');
    },
    resetState({ commit }) {
      return new Promise((resolve) => {
        commit('RESET_STATE');
        resolve();
      });
    },
    setLoading({ commit }, value) {
      commit('setLoading', value);
    },
    showToast({ commit }, { message, type, duration }) {
      commit('showToast', { message, type, duration });
      setTimeout(() => {
        commit('hideToast');
      }, duration || 3000);
    }
  },
  getters: {
    isLoading: state => state.isLoading,
    isAuthenticated: state => state.isAuthenticated,
    currentUser: state => state.user,
    hasActiveSubscription: state => !!state.subscription,
    subscriptionDetails: state => state.subscription,
    token: state => state.token,
    toast: state => state.toast,
    homePageData: state => state.homePageData
  }
}) 