<template>
  <div 
    ref="playerContainer" 
    class="relative w-full h-full"
  >
    <!-- Quality change notification -->
    <transition name="fade">
      <div 
        v-if="showQualityChangeNotification"
        class="absolute top-4 right-4 bg-black/80 text-white px-4 py-2 rounded-lg z-50 text-sm font-medium"
      >
        {{ qualityChangeMessage }}
      </div>
    </transition>

    <!-- Video Element -->
    <video
      ref="videoElement"
      class="w-full h-full object-contain"
      @timeupdate="onTimeUpdate"
      @loadedmetadata="onLoadedMetadata"
      @play="onPlay"
      @pause="onPause"
      @ended="onEnded"
      @error="onError"
      @waiting="onBuffering"
      @playing="onPlaying"
    ></video>

    <!-- Loading Spinner -->
    <div v-if="isLoading || isBuffering" class="absolute inset-0 flex items-center justify-center bg-black/50 z-10">
      <div class="w-16 h-16 border-4 border-[#c30059] border-t-transparent rounded-full animate-spin"></div>
    </div>

    <!-- Controls overlay -->
    <div 
      v-if="showingControls || isPaused" 
      class="absolute inset-0 bg-gradient-to-b from-black/50 via-transparent to-black/90 flex flex-col justify-between z-20"
      @click.stop="handleControlsClick"
    >
      <!-- Top bar -->
      <div class="p-4 flex justify-between items-center">
        <button @click="$emit('close')" class="text-white hover:text-[#c30059] video-control-button">
          <i class="fas fa-arrow-left text-2xl"></i>
        </button>
        <div class="text-white font-bold text-shadow">{{ title }}</div>
        <div class="flex gap-2">
          <!-- Live indicator -->
          <div v-if="isLive" class="flex items-center">
            <span class="h-2 w-2 bg-red-600 rounded-full animate-pulse mr-1.5"></span>
            <span class="text-white text-sm font-medium text-shadow">LIVE</span>
          </div>
          
          <!-- Mobile fullscreen button - only shown on mobile -->
          <button 
            @click.stop="toggleFullscreen" 
            class="text-white hover:text-[#c30059] video-control-button mobile-fullscreen-btn"
            title="Fullscreen"
          >
            <i :class="isFullscreen ? 'fas fa-compress-arrows-alt' : 'fas fa-expand-arrows-alt'" class="text-xl"></i>
          </button>
        </div>
      </div>

      <!-- Center play/pause button -->
      <div class="flex-1 flex items-center justify-center" @click.stop>
        <div class="controls-center-group" v-if="!isBuffering && !isLoading">
          <!-- Skip backward button -->
          <button @click.stop="skipBackward(10)" class="rounded-full border-2 border-white/70 w-12 h-12 flex flex-col items-center justify-center mx-3 control-shadow">
            <i class="fas fa-undo-alt"></i>
            <span class="text-xs">10</span>
          </button>
          
          <!-- Center play/pause button -->
          <button 
            @click.stop="togglePlayPause" 
            class="rounded-full border-2 border-white/70 w-16 h-16 flex items-center justify-center mx-3 control-shadow"
          >
            <i :class="isPaused ? 'fas fa-play' : 'fas fa-pause'" class="text-xl"></i>
          </button>
          
          <!-- Skip forward button -->
          <button @click.stop="skipForward(10)" class="rounded-full border-2 border-white/70 w-12 h-12 flex flex-col items-center justify-center mx-3 control-shadow">
            <i class="fas fa-redo-alt"></i>
            <span class="text-xs">10</span>
          </button>
        </div>
      </div>

      <!-- Bottom controls -->
      <div class="p-4 video-controls-bottom safe-area-bottom" @click.stop>
        <!-- Progress bar section -->
        <div class="relative mb-2">
          <!-- Preview thumbnail on hover -->
          <div 
            v-if="showPreview" 
            class="absolute bottom-8 transform -translate-x-1/2 bg-black/90 rounded-lg overflow-hidden shadow-xl preview-container"
            :style="{ left: `${previewPosition}%` }"
          >
            <!-- Video thumbnail/preview -->
            <div class="w-40 h-24 bg-[#1a1a1a] flex items-center justify-center overflow-hidden">
              <video 
                v-if="previewVideoRef"
                ref="previewVideoRef"
                :src="src"
                class="w-full h-full object-cover"
                :currentTime="previewTime"
                muted
              ></video>
              <div v-else class="w-full h-full bg-gradient-to-r from-[#1a1a1a] to-[#2a2a2a] flex items-center justify-center">
                <i class="fas fa-film text-[#c30059] text-2xl"></i>
              </div>
            </div>
            <!-- Time indicator -->
            <div class="px-3 py-1.5 text-center text-sm font-medium text-white/90 bg-black/95">
              {{ formatTime(previewTime) }}
            </div>
          </div>

          <!-- Progress bar -->
          <div 
            ref="progressBar"
            class="h-2 bg-white/30 rounded-full cursor-pointer group/progress relative overflow-hidden hover:h-3 transition-all duration-150"
            @mousemove="updatePreview"
            @mouseleave="hidePreview"
            @click="seek"
          >
            <!-- Buffered progress -->
            <div 
              class="absolute top-0 left-0 h-full bg-white/20 rounded-full"
              :style="{ width: `${bufferedPercentage}%` }"
            ></div>
            
            <!-- Played progress -->
            <div 
              class="absolute top-0 left-0 h-full bg-[#c30059] rounded-full"
              :style="{ width: `${playedPercentage}%` }"
            >
              <!-- Progress Knob -->
              <div class="absolute right-0 top-1/2 -translate-y-1/2 w-4 h-4 bg-[#c30059] border-2 border-white rounded-full transform scale-0 group-hover/progress:scale-100 transition-all duration-150"></div>
            </div>
          </div>
          
          <!-- Time indicators -->
          <div class="flex justify-between text-white/90 text-xs mt-2 text-shadow">
            <div>{{ formatTime(currentTime) }}</div>
            <div v-if="!isLive">{{ formatTime(duration) }}</div>
            <div v-else>LIVE</div>
          </div>
        </div>

        <!-- Bottom action buttons -->
        <div class="flex justify-between items-center mt-2">
          <div class="flex items-center">
            <span class="text-xs mr-1 text-shadow">{{ title }}</span>
          </div>
          
          <div class="flex items-center gap-3">
            <!-- Volume controls -->
            <div class="flex items-center gap-2 volume-controls">
              <!-- Mute button -->
              <button 
                @click="toggleMute" 
                class="icon-btn rounded-full border border-white/30 w-8 h-8 flex items-center justify-center"
                :title="isMuted ? 'Unmute' : 'Mute'"
              >
                <i :class="volumeIcon" class="text-sm"></i>
              </button>
              
              <!-- Volume slider -->
              <div class="volume-slider-container">
                <div class="volume-slider-wrapper">
                  <input 
                    type="range" 
                    v-model="volume" 
                    min="0" 
                    max="100" 
                    step="1"
                    @input="setVolume"
                    class="volume-slider"
                  />
                  <div class="volume-level-indicator">{{ volume }}%</div>
                </div>
              </div>
            </div>

            <!-- PiP button on larger screens -->
            <button 
              v-if="supportsPiP && !isMobileDevice"
              @click="togglePictureInPicture" 
              class="icon-btn rounded-full border border-white/30 w-8 h-8 flex items-center justify-center pip-button"
            >
              <i :class="isPiP ? 'fas fa-clone' : 'fas fa-clone'" class="text-sm"></i>
            </button>
            
            <!-- Settings button with quality menu -->
            <div class="relative">
              <button 
                @click.stop="toggleQualityMenu" 
                class="icon-btn rounded-full border border-white/30 w-8 h-8 flex items-center justify-center"
                title="Quality Settings"
              >
                <i class="fas fa-cog text-sm"></i>
                <span 
                  v-if="currentQuality !== 'auto'" 
                  class="absolute -top-1 -right-1 text-xs bg-[#c30059] rounded-full w-4 h-4 flex items-center justify-center"
                >
                  {{ currentQuality }}p
                </span>
              </button>
              
              <!-- Quality menu -->
              <div 
                v-if="showQualityMenu" 
                class="absolute bottom-full right-0 mb-2 bg-black/90 w-48 rounded-lg overflow-hidden z-50 shadow-lg"
                @click.stop
              >
                <div class="py-2">
                  <!-- Auto Quality Toggle -->
                  <div class="px-3 py-2 border-b border-white/10">
                    <div class="flex items-center justify-between">
                      <div>
                        <div class="text-white font-medium">Auto Quality</div>
                        <div class="text-xs text-white/50">Adjusts based on network</div>
                      </div>
                      <button 
                        @click="toggleAutoQuality"
                        class="relative w-10 h-5 rounded-full transition-colors duration-200"
                        :class="isAutoQuality ? 'bg-[#c30059]' : 'bg-gray-600'"
                      >
                        <span 
                          class="absolute w-4 h-4 bg-white rounded-full top-0.5 transition-transform duration-200"
                          :class="isAutoQuality ? 'right-0.5' : 'left-0.5'"
                        ></span>
                      </button>
                    </div>
                  </div>

                  <!-- Quality Options -->
                  <div class="py-1">
                    <div class="px-3 py-1 text-white/70 text-xs uppercase font-medium">Quality Options</div>
                    <button 
                      v-for="quality in availableQualities" 
                      :key="quality.height"
                      @click="setQuality(quality.height, false)" 
                      class="w-full px-3 py-2 text-left text-white hover:bg-white/10 flex justify-between items-center border-0 rounded-none transition-colors"
                      :class="{'text-[#c30059] bg-white/5': !isAutoQuality && currentQuality === quality.height}"
                    >
                      <span class="flex items-center">
                        <span>{{ quality.label }}</span>
                        <span class="ml-2 text-xs text-white/50">
                          {{ Math.round(quality.bitrate / 1000) }} Kbps
                        </span>
                      </span>
                      <i 
                        v-if="!isAutoQuality && currentQuality === quality.height" 
                        class="fas fa-check text-[#c30059]"
                      ></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
            
            <!-- Playback Speed button -->
            <div class="relative">
              <button 
                @click.stop="toggleSpeedMenu" 
                class="icon-btn rounded-full border border-white/30 w-8 h-8 flex items-center justify-center"
              >
                <i class="fas fa-tachometer-alt text-sm"></i>
              </button>
              
              <!-- Speed menu -->
              <div v-if="showSpeedMenu" class="absolute bottom-full right-0 mb-2 bg-black/90 w-48 rounded overflow-hidden z-50">
                <div class="py-2">
                  <div class="px-3 py-1 text-white/70 text-xs uppercase">Playback Speed</div>
                  <button
                    v-for="speed in playbackSpeeds" 
                    :key="speed"
                    @click="setPlaybackSpeed(speed)" 
                    class="w-full px-3 py-2 text-left text-white hover:bg-white/10 flex justify-between items-center border-0 rounded-none"
                    :class="{'text-[#c30059]': currentSpeed === speed}"
                  >
                    <span>{{ speed === 1 ? 'Normal' : speed + 'x' }}</span>
                    <i v-if="currentSpeed === speed" class="fas fa-check text-[#c30059]"></i>
                  </button>
                </div>
              </div>
            </div>
            
            <!-- Fullscreen toggle -->
            <button 
              v-if="isMobileDevice"
              @click="toggleFullscreen" 
              class="icon-btn rounded-full border border-white/30 w-8 h-8 flex items-center justify-center"
            >
              <i :class="isFullscreen ? 'fas fa-compress-arrows-alt' : 'fas fa-expand-arrows-alt'" class="text-sm"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Error message -->
    <div v-if="error" class="absolute inset-0 flex items-center justify-center bg-black/80 z-20">
      <div class="bg-black/90 p-6 rounded-lg max-w-md text-center">
        <i class="fas fa-exclamation-triangle text-[#c30059] text-4xl mb-4"></i>
        <h3 class="text-white text-xl font-bold mb-2">Playback Error</h3>
        <p class="text-white/80 mb-4">{{ error }}</p>
        <button 
          @click="retry" 
          class="bg-[#c30059] text-white py-2 px-4 rounded hover:bg-[#a30048] transition-colors"
        >
          Retry
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import Hls from 'hls.js';
import shaka from 'shaka-player';

export default {
  name: 'AdvancedVideoPlayer',
  props: {
    // Source URL for the video (HLS, DASH, or direct mp4)
    src: {
      type: String,
      required: true
    },
    // Title of the video
    title: {
      type: String,
      default: 'Video'
    },
    // Type of stream: 'hls', 'dash', or 'mp4'
    streamType: {
      type: String,
      default: 'hls',
      validator: (value) => ['hls', 'dash', 'mp4'].includes(value)
    },
    // Whether this is a live stream
    isLive: {
      type: Boolean,
      default: false
    },
    // DRM configuration (if needed)
    drmConfig: {
      type: Object,
      default: () => null
    },
    // Autoplay
    autoplay: {
      type: Boolean,
      default: true
    },
    // Controls visibility (managed by parent)
    controlsVisible: {
      type: Boolean,
      default: true
    },
    // CDN configuration for multi-CDN support
    cdnConfig: {
      type: Object,
      default: () => ({
        primary: 'https://cdn1.example.com',
        fallback: ['https://cdn2.example.com', 'https://cdn3.example.com'],
        chunkSize: 10 // seconds per chunk
      })
    },
    // Preload configuration
    preloadConfig: {
      type: Object,
      default: () => ({
        maxChunks: 3, // Number of chunks to preload ahead
        chunkSize: 10, // Size of each chunk in seconds
        bufferAhead: 30, // Buffer ahead in seconds
        lowLatencyMode: true, // Keep only this instance
        maxBufferLength: 60, // Maximum buffer length in seconds
        minBufferLength: 10, // Minimum buffer length in seconds
        maxMaxBufferLength: 600, // Maximum max buffer length in seconds
        maxBufferSize: 0, // Maximum buffer size in bytes (0 for unlimited)
        maxBufferHole: 0.5, // Maximum buffer hole in seconds
        highBufferWatchdogPeriod: 2, // High buffer watchdog period in seconds
        nudgeMaxRetry: 5, // Maximum number of retries for nudge
        nudgeOffset: 0.2, // Nudge offset in seconds
        startLevel: -1, // Start level (-1 for auto)
        abrEwmaDefaultEstimate: 500000, // Default bandwidth estimate in bits/s
        abrBandWidthFactor: 0.95, // Bandwidth factor
        abrBandWidthUpFactor: 0.7, // Bandwidth up factor
        abrMaxWithRealBitrate: true, // Use real bitrate for max level
        testBandwidth: true, // Test bandwidth
        progressive: true, // Progressive loading
        backBufferLength: 90 // Back buffer length in seconds
      }),
    },
    networkConfig: {
      type: Object,
      default: () => ({
        maxBufferLength: 30,
        maxMaxBufferLength: 600,
        maxBufferSize: 0,
        maxBufferHole: 0.5,
        lowLatencyMode: true, // Keep only this instance
        backBufferLength: 90,
        startLevel: -1,
        abrEwmaDefaultEstimate: 500000,
        abrBandWidthFactor: 0.95,
        abrBandWidthUpFactor: 0.7,
        abrMaxWithRealBitrate: true,
        testBandwidth: true,
        progressive: true
      }),
    },
  },
  data() {
    return {
      hls: null,
      shakaPlayer: null,
      isLoading: true,
      isBuffering: false,
      isPaused: !this.autoplay,
      currentTime: 0,
      duration: 0,
      bufferedPercentage: 0,
      playedPercentage: 0,
      volume: 100,
      isMuted: false,
      showControls: true,
      controlsTimeout: null,
      isFullscreen: false,
      availableQualities: [],
      currentQuality: 'auto',
      showQualityMenu: false,
      error: null,
      supportsPiP: false,
      isPiP: false,
      activeChunks: new Map(), // Currently loaded chunks
      preloadedChunks: new Set(),
      currentCdnIndex: 0,
      networkSpeed: 0,
      lastSpeedCheck: 0,
      chunkLoadTimes: [], // Track chunk load performance
      preloadWorker: null,
      streamManager: {
        currentQuality: 'auto',
        availableQualities: [],
        currentSegment: 0,
        bufferedSegments: [],
        preloadQueue: [],
        isPreloading: false,
        speedSamples: [], // Store recent speed measurements
        speedTrend: 'stable', // 'increasing', 'decreasing', 'stable'
        lastQualityChange: 0, // timestamp of last quality change
        rebufferingCount: 0, // track rebuffering events
        qualityChangeThreshold: 5000, // min ms between quality changes to avoid flickering
      },
      mountTimestamp: 0,
      isMobileDevice: false,
      autoLandscapeEnabled: true, // Enable auto landscape by default
      hasAutoRotated: false, // Track if we've already auto-rotated to avoid repeated calls
      showPreview: false,
      previewPosition: 0,
      previewTime: 0,
      previewVideoRef: null,
      currentChunk: 0,
      networkMonitorInterval: null,
      showVolumeSlider: false,
      showingControls: true,
      showSpeedMenu: false,
      currentSpeed: 1,
      playbackSpeeds: [0.25, 0.5, 0.75, 1, 1.25, 1.5, 1.75, 2],
      qualityLevels: [],
      isAutoQuality: true, // Track if auto quality is enabled
      userSelectedQuality: null, // Store user's manually selected quality
      qualityChangeHistory: [], // Track quality changes
      showQualityChangeNotification: false, // Show quality change notification
      qualityChangeMessage: '', // Message for quality notification
      lastQualityChange: 0, // Timestamp of last quality change
      isVideoClicked: false,
    };
  },
  computed: {
    volumeIcon() {
      if (this.isMuted || this.volume === 0) {
        return 'fas fa-volume-mute';
      } else if (this.volume < 50) {
        return 'fas fa-volume-down';
      } else {
        return 'fas fa-volume-up';
      }
    },
    currentCdnUrl() {
      const cdns = [this.cdnConfig.primary, ...this.cdnConfig.fallback];
      return cdns[this.currentCdnIndex];
    },
    currentQualityLabel() {
      if (this.currentQuality === 'auto') {
        return 'Auto'
      }
      return `${this.currentQuality}p`
    }
  },
  watch: {
    // Watch for play/pause state changes to emit events
    isPaused(newValue) {
      if (newValue) {
        this.$emit('pause');
      } else {
        this.$emit('play');
      }
    }
  },
  mounted() {
    // Initialize player based on stream type
    this.initializePlayer();
    
    // Check if mobile device
    this.detectMobileDevice();
    
    // Set initial volume
    if (localStorage.getItem('player-volume')) {
      this.volume = parseInt(localStorage.getItem('player-volume'));
    }
    this.setVolume();
    
    // Load auto landscape preference
    try {
      const savedAutoLandscape = localStorage.getItem('video-auto-landscape');
      if (savedAutoLandscape !== null) {
        this.autoLandscapeEnabled = savedAutoLandscape === 'true';
      }
    } catch (e) {
      console.warn('Could not load auto landscape preference');
    }
    
    // Add fullscreen change listener
    document.addEventListener('fullscreenchange', this.onFullscreenChange);
    
    // Add keyboard shortcuts
    window.addEventListener('keydown', this.handleKeydown);
    
    // Check PiP support
    this.checkPiPSupport();
    
    // Add PiP change listener
    document.addEventListener('webkitpresentationmodechanged', this.onPiPChange);
    document.addEventListener('pictureinpicturechange', this.onPiPChange);

    // Emit initial pause state
    this.$emit(this.isPaused ? 'pause' : 'play');

    this.initializeStreamManager();
    
    // Listen for screen orientation changes
    if (window.screen && window.screen.orientation) {
      window.screen.orientation.addEventListener('change', this.handleOrientationChange);
    }
    
    // Listen for resize events to re-check device type
    window.addEventListener('resize', this.detectMobileDevice);

    this.initializeQuality();

    // Load saved volume
    try {
      const savedVolume = localStorage.getItem('player-volume');
      if (savedVolume) {
        this.volume = parseInt(savedVolume);
        this.setVolume();
      }
    } catch (e) {
      console.warn('Could not load volume preference');
    }

    // Load saved playback speed
    try {
      const savedSpeed = localStorage.getItem('video-speed');
      if (savedSpeed) {
        this.setPlaybackSpeed(parseFloat(savedSpeed));
      }
    } catch (e) {
      console.warn('Could not load speed preference');
    }

    // Load user's preferred quality
    const savedQuality = localStorage.getItem('preferredQuality')
    if (savedQuality) {
      this.setQuality(savedQuality === 'auto' ? 'auto' : parseInt(savedQuality))
    }

    // Start with controls visible
    this.showingControls = true;
    this.resetControlsTimer();

    this.setupEventListeners();
  },
  beforeUnmount() {
    // Clean up resources
    this.destroyPlayer();
    
    // Remove event listeners
    document.removeEventListener('fullscreenchange', this.onFullscreenChange);
    window.removeEventListener('keydown', this.handleKeydown);
    window.removeEventListener('resize', this.detectMobileDevice);
    
    // Clear any pending timeouts
    if (this.controlsTimeout) {
      clearTimeout(this.controlsTimeout);
    }
    
    // Remove PiP listeners
    document.removeEventListener('webkitpresentationmodechanged', this.onPiPChange);
    document.removeEventListener('pictureinpicturechange', this.onPiPChange);

    if (this.preloadWorker) {
      this.preloadWorker.terminate();
    }

    // Clean up orientation change listener
    if (window.screen && window.screen.orientation) {
      window.screen.orientation.removeEventListener('change', this.handleOrientationChange);
    }

    // Clear network monitoring interval
    if (this._networkMonitoringInterval) {
      clearInterval(this._networkMonitoringInterval);
    }

    // Exit fullscreen if active
    if (document.fullscreenElement) {
      document.exitFullscreen().catch(err => {
        console.warn('Error exiting fullscreen:', err);
      });
    }

    this.cleanupEventListeners();
  },
  methods: {
    initializePlayer() {
      const video = this.$refs.videoElement;
      if (!video) {
        console.error('Video element not found');
        return;
      }
      
      // Reset state
      this.isLoading = true;
      this.error = null;

      // Log source URL for debugging
      console.log('Initializing player with source:', this.src);
      if (!this.src) {
        this.handleError('No video source provided');
        return;
      }
      
      try {
        switch (this.streamType) {
          case 'hls':
            console.log('Initializing HLS player...');
            this.initHlsPlayer();
            break;
          case 'dash':
            console.log('Initializing DASH player...');
            this.initDashPlayer();
            break;
          case 'mp4':
            console.log('Initializing direct player...');
            this.initDirectPlayer();
            break;
          default:
            this.handleError(`Unsupported stream type: ${this.streamType}`);
        }
      } catch (error) {
        console.error('Player initialization error:', error);
        this.handleError(error);
      }
    },
    
    initHlsPlayer() {
      if (Hls.isSupported()) {
        console.log('HLS.js is supported');
        if (this.hls) {
          console.log('Destroying existing HLS instance');
          this.hls.destroy();
        }

        // Configure HLS with strict quality control
        this.hls = new Hls({
          debug: false,
          enableWorker: true,
          lowLatencyMode: false,
          backBufferLength: 90,
          maxBufferSize: 0,
          maxBufferLength: 30,
          manifestLoadingTimeOut: 20000,
          manifestLoadingMaxRetry: 5,
          levelLoadingTimeOut: 20000,
          fragLoadingTimeOut: 20000,
          startLevel: -1,
          autoSwitchBitrate: false, // Disable auto bitrate switching
          abrEwmaDefaultEstimate: 1000000,
          abrBandWidthFactor: 0.95,
          abrBandWidthUpFactor: 0.7,
          abrMaxWithRealBitrate: true,
          minAutoBitrate: 300000,
          maxLoadingDelay: 4,
          maxStarvationDelay: 4,
          highBufferWatchdogPeriod: 2,
          nudgeOffset: 0.1,
          nudgeMaxRetry: 3,
          startFragPrefetch: true, // Enable fragment prefetching
          testBandwidth: false // Disable bandwidth testing when quality is locked
        });

        // Handle manifest parsing
        this.hls.on(Hls.Events.MANIFEST_PARSED, (event, data) => {
          console.log('M3U8 Manifest parsed, found levels:', data.levels);
          
          // Reset qualities array
          this.availableQualities = [];
          
          // Filter and add available qualities
          const qualities = data.levels
            .filter(level => level.height && level.bitrate > 100000)
            .map(level => ({
              height: level.height,
              label: `${level.height}p`,
              bitrate: level.bitrate,
              width: level.width
            }))
            .sort((a, b) => b.height - a.height);
          
          this.availableQualities = qualities;
          
          // Try to restore user's last selected quality
          try {
            const savedQuality = localStorage.getItem('userSelectedQuality');
            const savedAutoMode = localStorage.getItem('isAutoQuality');
            
            this.isAutoQuality = savedAutoMode === 'true';
            
            if (savedQuality && !this.isAutoQuality) {
              const quality = parseInt(savedQuality);
              const exists = qualities.find(q => q.height === quality);
              if (exists) {
                console.log('Restoring user selected quality:', quality + 'p');
                this.setQuality(quality, false);
                return;
              }
            }
          } catch (e) {
            console.warn('Could not restore quality preference:', e);
          }

          // Default to highest quality if no saved preference
          if (qualities.length > 0) {
            const highestQuality = qualities[0].height;
            console.log('Setting initial quality to highest:', highestQuality + 'p');
            this.setQuality(highestQuality, false);
          }
        });

        // Track quality changes
        this.hls.on(Hls.Events.LEVEL_SWITCHED, (event, data) => {
          const level = this.hls.levels[data.level];
          if (level) {
            const newQuality = level.height;
            const now = Date.now();
            
            // Only show notification if it's been more than 5 seconds since last change
            if (now - this.lastQualityChange > 5000) {
              this.showQualityChangeNotification = true;
              this.qualityChangeMessage = `Quality changed to ${newQuality}p`;
              setTimeout(() => {
                this.showQualityChangeNotification = false;
              }, 3000);
            }
            
            this.lastQualityChange = now;
            this.currentQuality = newQuality;
            
            // If not in auto mode and quality changed from user selection, force it back
            if (!this.isAutoQuality && this.userSelectedQuality && newQuality !== this.userSelectedQuality) {
              console.log('Forcing back to user selected quality:', this.userSelectedQuality + 'p');
              this.setQuality(this.userSelectedQuality, false);
            }
          }
        });

        // Monitor buffer health
        this.hls.on(Hls.Events.BUFFER_APPENDED, () => {
          this.checkBufferHealth();
        });

        console.log('Loading HLS source:', this.src);
        this.hls.loadSource(this.src);
        this.hls.attachMedia(this.$refs.videoElement);

        // Add media element event listeners
        this.$refs.videoElement.addEventListener('loadedmetadata', () => {
          console.log('Video metadata loaded');
          if (this.autoplay) {
            this.playVideo();
          }
        });
      } else if (this.$refs.videoElement.canPlayType('application/vnd.apple.mpegurl')) {
        // For Safari
        console.log('Using native HLS support for Safari');
        this.$refs.videoElement.src = this.src;
        this.$refs.videoElement.addEventListener('loadedmetadata', () => {
          console.log('Safari: Video metadata loaded');
          if (this.autoplay) {
            console.log('Safari: Attempting autoplay...');
            this.$refs.videoElement.play().catch(error => {
              console.warn('Safari: Autoplay prevented:', error);
              this.isPaused = true;
            });
          }
        });

        this.$refs.videoElement.addEventListener('error', () => {
          console.error('Safari video error:', this.$refs.videoElement.error);
          this.handleError('Error loading video in Safari: ' + (this.$refs.videoElement.error?.message || 'Unknown error'));
        });
      } else {
        this.handleError('HLS is not supported in this browser');
      }
    },
    
    initDashPlayer() {
      // Destroy any existing player first
      this.destroyPlayer();
      
      const video = this.$refs.videoElement;
      
      // Initialize Shaka Player
      shaka.polyfill.installAll();
      
      if (shaka.Player.isBrowserSupported()) {
        try {
          this.shakaPlayer = new shaka.Player(video);
          
          // Add event listeners
          this.shakaPlayer.addEventListener('error', this.onShakaError);
          
          // Configure ABR and Streaming for better performance on mobile/varied networks
          let shakaConfig = {
            abr: {
              enabled: true,
              defaultBandwidthEstimate: 300000, // Start with a conservative estimate (300 kbps)
              switchInterval: 5, // How often Shaka re-evaluates bandwidth (seconds)
              restrictions: {
                 minWidth: 0,
                 minHeight: 0,
                 maxWidth: Infinity,
                 maxHeight: Infinity,
                 minPixels: 0,
                 maxPixels: Infinity,
                 minFrameRate: 0,
                 maxFrameRate: Infinity,
                 minBandwidth: 0,
                 maxBandwidth: Infinity,
              }
            },
            streaming: {
              rebufferingGoal: 4, // Try to buffer 4 seconds when rebuffering
              bufferingGoal: 45, // Try to keep 45 seconds of content buffered ahead
              bufferBehind: 15, // Keep 15s behind current time
              alwaysStreamText: true,
            }
          };

          // Configure DRM if provided
          if (this.drmConfig) {
            shakaConfig.drm = {
                servers: this.drmConfig.servers || {},
                clearKeys: this.drmConfig.clearKeys || {}
            };
          }
          this.shakaPlayer.configure(shakaConfig);
          
          // Load the manifest
          this.shakaPlayer.load(this.src).then(() => {
            // Get available qualities
            const tracks = this.shakaPlayer.getVariantTracks();
            this.availableQualities = tracks.map(track => ({
              id: track.id,
              label: track.height ? `${track.height}p` : 'Audio Only',
              bitrate: track.bandwidth
            }));
            
            // Add auto quality option
            this.availableQualities.unshift({
              id: 'auto',
              label: 'Auto'
            });
            
            // Set initial quality to auto
            this.currentQuality = this.availableQualities[0];
            
            if (this.autoplay) {
              video.play().catch(error => {
                console.warn('Autoplay prevented', error);
                this.isPaused = true;
              });
            }
          }).catch(error => {
            this.handleError('Error loading DASH manifest: ' + error.message);
          });
        } catch (error) {
          this.handleError('Error initializing DASH player: ' + error.message);
        }
      } else {
        this.handleError('DASH is not supported in this browser');
      }
    },
    
    initDirectPlayer() {
      // For regular MP4 files
      console.log('Initializing direct player for MP4');
      const video = this.$refs.videoElement;
      
      if (!video) {
        console.error('Video element not found for MP4 playback');
        return;
      }

      // Validate source
      if (!this.src) {
        this.handleError('No video source provided for MP4 playback');
        return;
      }

      video.src = this.src;
      
      // Add error listener
      video.addEventListener('error', () => {
        console.error('MP4 video error:', video.error);
        this.handleError('Error playing MP4 video: ' + (video.error?.message || 'Unknown error'));
      });

      // Add loaded data listener
      video.addEventListener('loadeddata', () => {
        console.log('MP4 video data loaded');
      });
      
      if (this.autoplay) {
        console.log('MP4: Attempting autoplay...');
        video.play().catch(error => {
          console.warn('MP4: Autoplay prevented:', error);
          this.isPaused = true;
        });
      }
    },
    
    destroyPlayer() {
      // Clean up HLS player
      if (this.hls) {
        this.hls.destroy();
        this.hls = null;
      }
      
      // Clean up DASH player
      if (this.shakaPlayer) {
        this.shakaPlayer.destroy();
        this.shakaPlayer = null;
      }
      
      // Reset video element
      const video = this.$refs.videoElement;
      if (video) {
        video.src = '';
        video.removeAttribute('src');
      }
    },
    
    onTimeUpdate() {
      const video = this.$refs.videoElement;
      if (!video) return;
      
      this.currentTime = video.currentTime;
      
      // Calculate played percentage
      if (this.duration > 0) {
        this.playedPercentage = (this.currentTime / this.duration) * 100;
      }
      
      // Calculate buffered percentage
      if (video.buffered.length > 0) {
        const bufferedEnd = video.buffered.end(video.buffered.length - 1);
        this.bufferedPercentage = (bufferedEnd / this.duration) * 100;
      }
      
      // Check if we need to preload more chunks
      const currentTime = this.currentTime;
      const currentChunk = Math.floor(currentTime / this.cdnConfig.chunkSize);
      
      // Preload next chunks
      for (let i = 1; i <= this.preloadConfig.maxChunks; i++) {
        const chunkToPreload = currentChunk + i;
        if (!this.preloadedChunks.has(chunkToPreload)) {
          this.preloadChunk(chunkToPreload);
        }
      }
      
      // Clean up old chunks
      this.cleanupOldChunks(currentChunk);
    },
    
    onLoadedMetadata() {
      const video = this.$refs.videoElement;
      if (!video) return;
      
      this.duration = video.duration;
      this.isLoading = false;
    },
    
    onPlay() {
      this.isPaused = false;
      
      // If on mobile and auto landscape is enabled, go to landscape mode
      if (this.isMobileDevice && this.autoLandscapeEnabled && !this.hasAutoRotated) {
        this.enterAutoLandscapeMode();
      }
    },
    
    onPause() {
      this.isPaused = true;
    },
    
    onEnded() {
      this.isPaused = true;
      // If we auto-rotated to landscape, exit fullscreen when video ends
      if (this.isMobileDevice && this.hasAutoRotated && this.isFullscreen) {
        this.exitAutoLandscapeMode();
      }
      this.$emit('ended');
    },
    
    onBuffering() {
      this.isBuffering = true;
    },
    
    onPlaying() {
      this.isBuffering = false;
    },
    
    onError() {
      this.handleError('Video playback error');
    },
    
    onShakaError(event) {
      this.handleError('DASH playback error: ' + event.detail.message);
    },
    
    handleError(message) {
      console.error('Player error:', message);
      this.error = message;
      this.isLoading = false;
      this.isBuffering = false;
      
      // Emit error event to parent
      this.$emit('error', message);
      
      // Try to show a more user-friendly error message
      if (message.includes('network')) {
        this.error = 'Please check your internet connection and try again';
      } else if (message.includes('format') || message.includes('supported')) {
        this.error = 'This video format is not supported by your browser';
      } else if (message.includes('source')) {
        this.error = 'Unable to load the video. Please try again later';
      }
    },
    
    seek(event) {
      const video = this.$refs.videoElement;
      const progressBar = this.$refs.progressBar;
      if (!video || !progressBar || this.isLive) return;
      
      const rect = progressBar.getBoundingClientRect();
      const pos = (event.clientX - rect.left) / rect.width;
      
      video.currentTime = pos * this.duration;
    },
    
    togglePlayPause() {
      const video = this.$refs.videoElement;
      if (!video) return;
      
      if (this.isPaused) {
        video.play().catch(error => {
          console.warn('Play failed:', error);
        });
      } else {
        video.pause();
      }
    },
    
    toggleMute() {
      const video = this.$refs.videoElement;
      if (!video) return;
      
      this.isMuted = !this.isMuted;
      video.muted = this.isMuted;
      
      // If unmuting, restore previous volume
      if (!this.isMuted && this.volume === 0) {
        this.volume = 100;
        this.setVolume();
      }
    },
    
    setVolume() {
      const video = this.$refs.videoElement;
      if (!video) return;
      
      // Ensure volume is within bounds
      this.volume = Math.max(0, Math.min(100, this.volume));
      
      video.volume = this.volume / 100;
      video.muted = this.volume === 0;
      this.isMuted = video.muted;
      
      // Save volume preference
      try {
      localStorage.setItem('player-volume', this.volume.toString());
      } catch (e) {
        console.warn('Could not save volume preference');
      }
    },
    
    toggleQualityMenu() {
      this.showQualityMenu = !this.showQualityMenu;
      if (this.showQualityMenu) {
        this.resetControlsTimer();
      }
    },
    
    setQuality(height, allowAuto = true) {
      console.log('Setting quality to:', height, 'allowAuto:', allowAuto);
      
      if (!this.hls) {
        console.warn('HLS not initialized');
        return;
      }
      
      try {
        if (height === 'auto' && allowAuto) {
          console.log('Enabling automatic quality selection');
          this.isAutoQuality = true;
          this.userSelectedQuality = null;
          this.hls.currentLevel = -1;
          this.currentQuality = 'auto';
          
          // Enable ABR for auto mode
          this.hls.config.autoSwitchBitrate = true;
          this.hls.config.testBandwidth = true;
          
          localStorage.setItem('isAutoQuality', 'true');
          localStorage.removeItem('userSelectedQuality');
        } else {
          // Find the level that matches the requested height
          const levelIndex = this.hls.levels.findIndex(level => level.height === height);
          
          if (levelIndex !== -1) {
            console.log('Switching to quality level:', levelIndex, 'Height:', height + 'p');
            this.isAutoQuality = false;
            this.userSelectedQuality = height;
            
            // Disable ABR and force quality level
            this.hls.config.autoSwitchBitrate = false;
            this.hls.config.testBandwidth = false;
            this.hls.currentLevel = levelIndex;
            this.hls.loadLevel = levelIndex;
            this.hls.nextLevel = levelIndex;
            this.hls.config.startLevel = levelIndex;
            
            this.currentQuality = height;
            
            // Save user preference
            localStorage.setItem('isAutoQuality', 'false');
            localStorage.setItem('userSelectedQuality', height.toString());
          } else {
            console.warn('Requested quality level not found:', height);
          }
        }
        
        // Close quality menu
        this.showQualityMenu = false;
        
      } catch (error) {
        console.error('Error setting quality:', error);
      }
    },
    
    initializeQuality() {
      try {
        const savedQuality = localStorage.getItem('video-quality');
        if (savedQuality) {
          const quality = this.availableQualities.find(q => q.id === savedQuality);
          if (quality) {
            this.setQuality(quality.height);
          }
        }
      } catch (e) {
        console.warn('Could not load quality preference');
      }
    },
    
    resetControlsTimer() {
      if (this.controlsTimeout) {
        clearTimeout(this.controlsTimeout);
      }
      
      // Hide controls after 3 seconds
      this.controlsTimeout = setTimeout(() => {
        if (!this.isPaused) {
          this.showingControls = false;
        }
      }, 3000);
    },
    
    async toggleFullscreen() {
      const container = this.$refs.playerContainer;
      if (!container) return;
      
      try {
        if (!document.fullscreenElement) {
          await container.requestFullscreen();
          this.isFullscreen = true;
          
          // Force landscape orientation for mobile devices
          if (this.isMobileDevice) {
            // Try multiple methods to ensure orientation change works
            if (window.screen && window.screen.orientation) {
              try {
                await window.screen.orientation.lock('landscape');
              } catch (err) {
                console.warn('Screen orientation API lock failed:', err);
              }
            }
            
            // Fallback for older devices
            if (window.orientation !== undefined) {
              window.addEventListener('orientationchange', this.handleOrientationChange);
            }
            
            // Send message to Flutter
            try {
              window.flutter_inappwebview?.callHandler('forceOrientation', 'landscape');
            } catch (err) {
              console.warn('Flutter handler not available:', err);
            }
          }
        } else {
          await document.exitFullscreen();
          this.isFullscreen = false;
          
          // Reset orientation for mobile devices
          if (this.isMobileDevice) {
            if (window.screen && window.screen.orientation) {
              try {
                await window.screen.orientation.unlock();
              } catch (err) {
                console.warn('Screen orientation API unlock failed:', err);
              }
            }
            
            // Remove orientation change listener
            if (window.orientation !== undefined) {
              window.removeEventListener('orientationchange', this.handleOrientationChange);
            }
            
            // Send message to Flutter
            try {
              window.flutter_inappwebview?.callHandler('forceOrientation', 'auto');
            } catch (err) {
              console.warn('Flutter handler not available:', err);
            }
          }
        }
      } catch (error) {
        console.error('Fullscreen toggle error:', error);
      }
    },
    
    onFullscreenChange() {
      this.isFullscreen = !!document.fullscreenElement;
      console.log('Fullscreen state changed:', this.isFullscreen);
    },
    
    formatTime(seconds) {
      if (!seconds || isNaN(seconds)) return '0:00';
      
      const mins = Math.floor(seconds / 60);
      const secs = Math.floor(seconds % 60);
      return `${mins}:${secs < 10 ? '0' : ''}${secs}`;
    },
    
    retry() {
      this.error = null;
      
      // For DASH errors, allow the parent to change src and streamType before retry
      setTimeout(() => {
        this.initializePlayer();
      }, 100); // Small delay to ensure props are updated
    },
    
    handleKeydown(event) {
      // Only handle keyboard events when this component is mounted
      if (!this.$refs.videoElement) return;
      
      switch (event.key.toLowerCase()) {
        case ' ':
        case 'k':
          event.preventDefault();
          this.togglePlayPause();
          break;
        case 'f':
          event.preventDefault();
          this.toggleFullscreen();
          break;
        case 'm':
          event.preventDefault();
          this.toggleMute();
          break;
        case 'arrowright':
          if (!this.isLive) {
            event.preventDefault();
            const video = this.$refs.videoElement;
            if (video) {
              video.currentTime = Math.min(video.currentTime + 10, video.duration);
            }
          }
          break;
        case 'arrowleft':
          if (!this.isLive) {
            event.preventDefault();
            const video = this.$refs.videoElement;
            if (video) {
              video.currentTime = Math.max(video.currentTime - 10, 0);
            }
          }
          break;
        case 'arrowup':
          event.preventDefault();
          this.volume = Math.min(this.volume + 5, 100);
          this.setVolume();
          break;
        case 'arrowdown':
          event.preventDefault();
          this.volume = Math.max(this.volume - 5, 0);
          this.setVolume();
          break;
      }
    },
    
    checkPiPSupport() {
      const video = this.$refs.videoElement;
      if (video) {
        this.supportsPiP = document.pictureInPictureEnabled || 
                          'webkitPresentationMode' in video ||
                          'webkitSupportsPresentationMode' in video;
      }
    },
    
    async togglePictureInPicture() {
      const video = this.$refs.videoElement;
      if (!video || !this.supportsPiP) return;
      
      try {
        if (document.pictureInPictureElement) {
          await document.exitPictureInPicture();
        } else {
          await video.requestPictureInPicture();
        }
      } catch (error) {
        console.error('PiP failed:', error);
      }
    },
    
    onPiPChange() {
      this.isPiP = !!document.pictureInPictureElement;
    },
    
    // Skip forward by specified seconds
    skipForward(seconds) {
      const video = this.$refs.videoElement;
      if (video) {
        // Calculate new time and ensure it doesn't exceed duration
        const newTime = Math.min(video.currentTime + seconds, video.duration || 0);
        video.currentTime = newTime;
        this.showControlsOnInteraction();
      }
    },
    
    // Skip backward by specified seconds
    skipBackward(seconds) {
      const video = this.$refs.videoElement;
      if (video) {
        // Calculate new time and ensure it doesn't go below 0
        const newTime = Math.max(video.currentTime - seconds, 0);
        video.currentTime = newTime;
        this.showControlsOnInteraction();
      }
    },
    
    // Show controls when user interacts with player
    showControlsOnInteraction() {
      this.$emit('interaction');
    },
    
    async initializeStreamManager() {
      // Initialize Web Worker for background preloading
      this.preloadWorker = new Worker(new URL('./preloadWorker.js', import.meta.url));
      this.preloadWorker.onmessage = this.handlePreloadComplete.bind(this);
      
      // Initialize chunk management
      await this.initializeChunkSystem();
      
      // Start network speed monitoring
      this.startNetworkMonitoring();
    },
    
    async initializeChunkSystem() {
      if (!this.cdnConfig.useCDN) return;
      
      // Initialize chunk tracking
      this.currentChunk = 0;
      this.preloadedChunks.clear();
      
      // Start preloading initial chunks
      for (let i = 0; i < this.cdnConfig.maxChunks; i++) {
        await this.preloadChunk(i);
      }
    },
    
    async preloadChunk(chunkIndex) {
      if (!this.cdnConfig.useCDN || this.preloadedChunks.has(chunkIndex)) return;
      
      try {
        const chunkUrl = `${this.cdnConfig.cdnUrl}/chunks/${chunkIndex}.ts`;
        const response = await fetch(chunkUrl);
        const chunkData = await response.blob();
        
        // Store the chunk data in memory
        this.activeChunks.set(chunkIndex, chunkData);
        this.preloadedChunks.add(chunkIndex);
        
        // Clean up old chunks if needed
        this.cleanupOldChunks(this.currentChunk);
        } catch (error) {
        console.warn(`Failed to preload chunk ${chunkIndex}:`, error);
      }
    },
    
    cleanupOldChunks(currentChunk) {
      const chunksToKeep = new Set();
      
      // Keep chunks within the cleanup threshold
      for (let i = Math.max(0, currentChunk - Math.floor(this.cdnConfig.cleanupThreshold * this.cdnConfig.maxChunks));
           i <= currentChunk + Math.floor(this.cdnConfig.preloadThreshold * this.cdnConfig.maxChunks);
           i++) {
        chunksToKeep.add(i);
      }
      
      // Remove chunks that are too old
      for (const chunk of this.preloadedChunks) {
        if (!chunksToKeep.has(chunk)) {
          this.preloadedChunks.delete(chunk);
        }
      }
    },
    
    handlePreloadComplete(event) {
      const { chunkIndex, success } = event.data;
      if (success) {
        this.preloadedChunks.add(chunkIndex);
      }
    },
    
    startNetworkMonitoring() {
      // Monitor network speed using HLS.js bandwidth estimation
      this.networkMonitorInterval = setInterval(() => {
        if (this.hls) {
          // Get bandwidth estimate from HLS.js
          const bandwidthEstimate = this.hls.bandwidthEstimate;
          if (bandwidthEstimate) {
            // Convert to Mbps
            this.networkSpeed = bandwidthEstimate / 1000000;
            this.adjustVideoQuality();
          }
        }
      }, 5000); // Check every 5 seconds
    },
    
    adjustVideoQuality() {
      if (!this.hls || !this.availableQualities) return;
      
      const levels = this.hls.levels;
      if (!levels || levels.length === 0) return;
      
      // Find the best quality level based on network speed
      let targetLevel = -1;
      if (this.networkSpeed > 5) { // 5 Mbps
        targetLevel = levels.length - 1; // Highest quality
      } else if (this.networkSpeed > 2) { // 2 Mbps
        targetLevel = Math.floor(levels.length / 2); // Medium quality
          } else {
        targetLevel = 0; // Lowest quality
      }
      
      // Apply the quality level if it's different from current
      if (targetLevel !== this.hls.currentLevel) {
        this.hls.currentLevel = targetLevel;
      }
    },
    
    detectMobileDevice() {
      // Check based on screen size
      const isMobileBySize = window.innerWidth < 768 || window.innerHeight < 768;
      
      // Check based on user agent (more reliable)
      const isMobileByUA = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
      
      // Check if touch is supported
      const isTouchDevice = 'ontouchstart' in window || navigator.maxTouchPoints > 0;
      
      // Combined check - device is considered mobile if it meets at least two conditions
      this.isMobileDevice = (isMobileBySize && isTouchDevice) || isMobileByUA;
      
      console.log('Device detection:', { isMobile: this.isMobileDevice });
    },
    handleOrientationChange() {
      // If user manually changes orientation while in fullscreen, respect that choice
      const orientation = window.screen && window.screen.orientation ? 
        window.screen.orientation.type : null;
      
      if (this.isMobileDevice && this.isFullscreen) {
        if (orientation && orientation.includes('portrait')) {
          // User has manually rotated to portrait while in fullscreen
          // Just track this but don't force back to landscape
          console.log('User chose portrait orientation while in fullscreen');
        }
      }
    },
    // Method to enter auto landscape fullscreen mode
    async enterAutoLandscapeMode() {
      const container = this.$refs.playerContainer;
      if (!container) return;
      
      try {
        // Don't do this again until video is restarted
        this.hasAutoRotated = true;
        
        // First enter fullscreen
        await container.requestFullscreen();
        this.isFullscreen = true;
        
        // Then lock to landscape orientation
        if (screen.orientation && screen.orientation.lock) {
          try {
            await screen.orientation.lock('landscape');
            console.log('Auto entered landscape mode');
          } catch (orientationError) {
            console.warn('Could not auto lock orientation:', orientationError);
          }
        }
      } catch (error) {
        console.error('Auto landscape fullscreen error:', error);
      }
    },
    
    // Method to exit auto landscape mode
    async exitAutoLandscapeMode() {
      try {
        // First try to unlock orientation
        if (screen.orientation && screen.orientation.unlock) {
          try {
            screen.orientation.unlock();
            console.log('Orientation unlocked');
          } catch (err) {
            console.warn('Error unlocking orientation:', err);
          }
        }
        
        // Small delay to let orientation change take effect
        await new Promise(resolve => setTimeout(resolve, 100));
        
        // Then exit fullscreen
        if (document.fullscreenElement) {
          try {
            await document.exitFullscreen();
          } catch (err) {
            console.warn('Error exiting fullscreen:', err);
          }
        }
        
        this.isFullscreen = false;
        this.hasAutoRotated = false;
        
        // Reset any player state as needed
        this.$emit('interaction'); // Show controls
      } catch (error) {
        console.error('Error exiting auto landscape mode:', error);
      }
    },
    // Toggle auto landscape feature
    toggleAutoLandscape() {
      this.autoLandscapeEnabled = !this.autoLandscapeEnabled;
      
      // Save preference to localStorage
      try {
        localStorage.setItem('video-auto-landscape', this.autoLandscapeEnabled.toString());
      } catch (e) {
        console.warn('Could not save auto landscape preference');
      }
      
      // If turning on while video is already playing and not in landscape, 
      // trigger landscape mode immediately
      if (this.autoLandscapeEnabled && !this.isPaused && !this.hasAutoRotated) {
        this.enterAutoLandscapeMode();
      }
      
      // If turning off while in auto landscape mode, exit it
      if (!this.autoLandscapeEnabled && this.hasAutoRotated && this.isFullscreen) {
        this.exitAutoLandscapeMode();
      }
    },
    updatePreview(event) {
      const progressBar = this.$refs.progressBar;
      if (!progressBar || this.isLive) return;
      
      const rect = progressBar.getBoundingClientRect();
      const position = ((event.clientX - rect.left) / rect.width);
      
      // Update preview position (in percentage)
      this.previewPosition = position * 100;
      
      // Calculate preview time
      this.previewTime = position * this.duration;
      
      // Show preview
      this.showPreview = true;
      
      // Try to show actual video frame (if supported)
      this.$nextTick(() => {
        const previewVideo = this.$refs.previewVideoRef;
        if (previewVideo) {
          previewVideo.currentTime = this.previewTime;
        }
      });
    },
    hidePreview() {
      this.showPreview = false;
    },
    checkBufferHealth() {
      const video = this.$refs.videoElement;
      if (!video || !video.buffered.length) return;

      const currentTime = video.currentTime;
      const bufferedEnd = video.buffered.end(video.buffered.length - 1);
      const bufferLength = bufferedEnd - currentTime;

      console.log('Buffer health check - Length:', bufferLength.toFixed(2), 'seconds');

      // If buffer is low and we're not in auto mode, increase buffer size
      if (bufferLength < 2 && !this.isAutoQuality) {
        console.log('Buffer is low, increasing buffer size...');
        this.hls.config.maxBufferLength = 60;
        this.hls.config.maxMaxBufferLength = 600;
      } else if (bufferLength > 30) {
        // Reset buffer size when we have enough buffer
        this.hls.config.maxBufferLength = 30;
        this.hls.config.maxMaxBufferLength = 60;
      }
    },
    toggleControls() {
      this.showingControls = !this.showingControls;
      this.resetControlsTimer();
    },
    toggleSpeedMenu() {
      this.showSpeedMenu = !this.showSpeedMenu;
      if (this.showSpeedMenu) {
        this.resetControlsTimer();
      }
    },
    setPlaybackSpeed(speed) {
      const video = this.$refs.videoElement;
      if (!video) return;
      
      this.currentSpeed = speed;
      video.playbackRate = speed;
      this.showSpeedMenu = false;
      
      // Save speed preference
      try {
        localStorage.setItem('video-speed', speed.toString());
      } catch (e) {
        console.warn('Could not save speed preference');
      }
    },
    // Add new methods for error handling
    handleNetworkError(data) {
      console.log('Attempting to recover from network error...');
      switch (data.details) {
        case Hls.ErrorDetails.MANIFEST_LOAD_ERROR:
        case Hls.ErrorDetails.MANIFEST_LOAD_TIMEOUT:
          // Try to reload manifest
          setTimeout(() => {
            console.log('Reloading manifest...');
            this.hls.loadSource(this.src);
          }, 1000);
          break;
        case Hls.ErrorDetails.FRAG_LOAD_ERROR:
        case Hls.ErrorDetails.FRAG_LOAD_TIMEOUT:
          // Try to recover fragment error
          this.hls.recoverMediaError();
          break;
        default:
          // For other network errors, try to recover
          this.hls.startLoad();
          break;
      }
    },
    handleMediaError(data) {
      console.log('Attempting to recover from media error...');
      if (data.details === Hls.ErrorDetails.BUFFER_STALLED_ERROR) {
        // Try to recover from buffer stall
        this.hls.recoverMediaError();
      } else {
        // For other media errors
        this.hls.recoverMediaError();
      }
    },
    playVideo() {
      console.log('Attempting to play video...');
      const playPromise = this.$refs.videoElement.play();
      
      if (playPromise !== undefined) {
        playPromise.then(() => {
          console.log('Video playback started successfully');
          this.isPaused = false;
        }).catch(error => {
          console.warn('Video playback failed:', error);
          this.isPaused = true;
          // Show user-friendly error
          this.handleError('Video playback failed. Please try again.');
        });
      }
    },
    // Add quality change notification component
    toggleAutoQuality() {
      this.isAutoQuality = !this.isAutoQuality;
      if (this.isAutoQuality) {
        this.setQuality('auto');
      } else if (this.userSelectedQuality) {
        this.setQuality(this.userSelectedQuality);
      }
    },
    // New methods for Netflix-style menu behavior
    handleVideoContainerClick(event) {
      // Only handle clicks on the video container, not on controls
      if (event.target === this.$refs.playerContainer || event.target === this.$refs.videoElement) {
        this.isVideoClicked = true;
        this.closeAllMenus();
        this.toggleControls();
      }
    },

    closeAllMenus() {
      this.showQualityMenu = false;
      // Add any future menus here
    },

    handleKeyPress(event) {
      if (event.key === 'Escape') {
        this.closeAllMenus();
      }
    },

    handleControlsClick(event) {
      // Prevent clicks on controls from closing menus
      event.stopPropagation();
    },

    setupEventListeners() {
      // Add event listeners when component mounts
      document.addEventListener('keydown', this.handleKeyPress);
      if (this.$refs.playerContainer) {
        this.$refs.playerContainer.addEventListener('click', this.handleVideoContainerClick);
      }
    },

    cleanupEventListeners() {
      // Remove event listeners when component unmounts
      document.removeEventListener('keydown', this.handleKeyPress);
      if (this.$refs.playerContainer) {
        this.$refs.playerContainer.removeEventListener('click', this.handleVideoContainerClick);
      }
    }
  }
};
</script>

